.bf-skeleton-rect {
  height: var(--bfs24);
}

.bf-skeleton-rect,
.bf-skeleton-text {
  background-color: hsla(var(--bfc-base-c-inverted-hsl), 0.1);
  position: relative;
  overflow: hidden;
}

.bf-skeleton-rect::after,
.bf-skeleton-text::after {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  content: '';
  position: absolute;
  animation: wave 1.5s linear 0.5s infinite;
  transform: translate3d(-100%, 0, 0px);
  background: linear-gradient(
    90deg,
    transparent,
    hsla(var(--bfc-base-c-inverted-hsl), 0.2),
    transparent
  );
}

.bf-skeleton-text {
  border-radius: 3px;
  color: transparent;
  transform: scale(1, 0.6);
  display: block;
}

@keyframes wave {
  0% {
    transform: translate3d(-100%, 0, 0px);
  }

  60% {
    transform: translate3d(100%, 0, 0px);
  }

  100% {
    transform: translate3d(100%, 0, 0px);
  }
}
