body.ReactModal__Body--open {
  overflow: hidden;
}

.bf-modal-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: var(--bfc-overlay);
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: center;
}

.bf-modal {
  position: relative;
  outline: none;
}

.bf-modal-content {
  background-color: var(--bfc-base);
  color: var(--bft-base);
  border: 1px solid var(--bfc-base-dimmed);
  border-radius: var(--bfl-border-radius);
  max-height: 90vh;
  max-width: 90vw;
  overflow: auto;
}

.bf-modal-content img {
  max-width: 100%;
  max-height: 100%;
}

.bf-modal-content-container {
  padding: var(--bfl-modal-padding);
  padding-top: 0;
}

.bf-modal-content.bf-modal-no-header .bf-modal-content-container {
  padding: var(--bfl-modal-padding);
}

.bf-modal-content.bf-no-padding .bf-modal-content-container {
  padding: 0;
}

.bf-modal-header {
  padding: var(--bfl-modal-padding);
  padding-bottom: var(--bfs12);
}

.bf-modal-header-h1 {
  display: inline-block;
  font-style: normal;
  font-weight: normal;
  font-size: var(--bf-font-size-xxxl);
  margin: 0;
}

.bf-modal-header-icon {
  display: inline-block;
  padding-right: 8px;
  font-style: normal;
  font-weight: 300;
  font-size: var(--bf-font-size-xxxl);
  color: var(--bfc-theme-fade-c);
}

.bf-modal-footer {
  display: flex;
  justify-content: flex-end;
  padding-top: var(--bfs24);
}

.bf-modal {
  margin: 0;
}
.bf-modal .bf-close-button {
  display: none;
}

/* full screen modal for small screen widths, or in landscape (small screen *height*) */
@media (max-width: 599.9px), (max-height: 599.9px) {
  .bf-modal {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
  }
  .bf-modal-content {
    border: none;
    border-radius: none;
    max-height: none;
    max-width: none;
    min-height: 100%;
  }
  .bf-modal-header {
    padding-right: 64px;
  }
  .bf-modal .bf-close-button {
    display: block;
  }
  .bf-modal-content.bf-no-padding .bf-close-button {
    position: absolute;
    margin: 0;
    right: var(--bfs16);
    top: var(--bfs24);
  }
  .bf-modal-close-button {
    display: none;
  }
}
