.bf-message {
  padding: var(--bfs12);
  border-radius: var(--bfl-border-radius);
  font-size: var(--bf-font-size-m);
}
.bf-message-header {
  margin-bottom: var(--bfs4);
  font-weight: 600;
}
.bf-message-icon {
  margin-right: var(--bfs4);
}
.bf-message-statusbar {
  padding-top: var(--bfs8);
  padding-bottom: var(--bfs8);
  font-weight: 600;
  position: relative;
}

.bf-message-statusbar .bf-message-content {
  display: inline;
}

.bf-message-close,
.bf-message-expandable-icon {
  float: right;
  position: relative;
  background-color: transparent;
  color: inherit;
  border: 1px dashed transparent;
  border-radius: 21px;
  padding: 0;
  text-align: center;
  cursor: pointer;
  padding: 0;
  height: 21px;
  width: 21px;
  line-height: 21px;
}

.bf-message-close:focus {
  outline: none;
}

.bf-message-close[data-focus-visible-added] {
  border-color: currentColor;
}

/* remove firefox dotted line on focus*/
.bf-message-close::-moz-focus-inner {
  border: 0;
}

/*  close button hover state colors */
.bfc-theme-fade-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-theme-fade-c-hsl), 0.25);
}
.bfc-success-fade-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-success-fade-c-hsl), 0.25);
}
.bfc-warning-fade-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-warning-fade-c-hsl), 0.25);
}
.bfc-alert-fade-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-alert-fade-c-hsl), 0.25);
}

.bfc-theme-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-theme-c-hsl), 0.25);
}
.bfc-success-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-success-c-hsl), 0.25);
}
.bfc-warning-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-warning-c-hsl), 0.25);
}
.bfc-alert-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-alert-c-hsl), 0.25);
}
.bfc-dimmed-bg .bf-message-close:hover {
  background-color: hsla(var(--bfc-base-c-hsl), 0.25);
}

.bf-message > ol:last-child,
.bf-message > ul:last-child {
  margin-bottom: 0;
}

.bf-message-expandable-content {
  background-color: var(--bfc-base-3);
  word-wrap: break-word;
  font-size: var(--bf-font-size-m);
  padding: var(--bfs16);
}

.bf-message-expandable-content.bf-nopadding {
  padding: 0;
}

.bf-message-expandable-active .bf-message-expandable-icon > svg {
  transform: rotate(-180deg);
}

.bf-message-expandable-icon > svg {
  transition: all var(--bf-animation-duration) var(--bf-timing-function);
}

.bf-message-expandable.bf-message-expandable-active {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.bf-message-expandable {
  border-bottom: 2px solid transparent;
  padding: 12px 12px 10px 12px;
  cursor: pointer;
  outline: none;
}

.bf-message-expandable:hover {
  border-bottom: 2px solid;
}
.bf-message-expandable[data-focus-visible-added] {
  outline: 1px dashed;
  -moz-outline-radius: var(--bfl-border-radius);
}

.bf-message-expandable .bf-message-header {
  margin: 0;
  font-weight: 400;
}

.bf-message > :last-child {
  margin-bottom: 0;
}
.bf-message-header + * {
  margin-top: 0;
}
