/* Use cases */
/* fullscreen on mobile without overlay */
/* overlay on landscape tablet */
/* landscape to portrait change should keep nav hidden */
/* not animate on page load */

:root {
  --bf-nav-top-height: var(--bfs56);
  --bf-nav-side-width: 14.375rem;
  --bf-nav-side-transition: var(--bf-animation-duration)
    var(--bf-timing-function);
  --bf-nav-mobile-width: 100%;
}
.bf-nav-side-collapsed,
.bf-nav-side-collapsed ~ * {
  --bf-nav-side-width: var(--bfs56);
}
/* when nav-side is hidden with .from-medium or .from-large,
make sure nav-top and content goes all the way to the left */
@media (max-width: 959.9px) {
  .bf-nav-side.from-medium,
  .bf-nav-side.from-medium ~ * {
    --bf-nav-side-width: 0px;
  }
  .bf-nav-mobile-overlay {
    display: none;
  }
}
@media (max-width: 1279.9px) {
  .bf-nav-side.from-large,
  .bf-nav-side.from-large ~ * {
    --bf-nav-side-width: 0px;
  }
}
@media (max-width: 1599.9px) {
  .bf-nav-side.from-xl,
  .bf-nav-side.from-xl ~ * {
    --bf-nav-side-width: 0px;
  }
  body.bf-nav-mobile-open {
    overflow: hidden;
  }
}

/* display small 'it' logo for nav-side when collapsed (default hidden) */
.bf-small-it-logo {
  display: none;
}
.bf-nav-side-collapsed.bf-nav-side .bf-small-it-logo {
  display: inline;
}
.bf-nav-side-collapsed.bf-nav-side .bf-full-intility-logo {
  display: none;
}

/* separator between items */
.bf-nav > hr,
.bf-nav-side-content > hr,
.bf-nav-mobile > hr,
.bf-nav-group-content > hr,
.bf-nav-group-dropout-content > hr {
  padding: 0;
  margin: 0;
  border: var(--bfl-border);
  border-bottom: none;
}
.bf-nav-mobile > hr,
.bf-nav-mobile .bf-nav > hr {
  margin-top: var(--bfs8);
  border-color: transparent;
}

/* mobile fullscreen popup */
.bf-nav-mobile {
  position: fixed;
  top: var(--bf-nav-top-height);
  right: 0;
  bottom: 0;
  left: 0;
  overflow: auto;
  z-index: 99999;
  background-color: var(--bfc-base-2);
  font-size: var(--bf-font-size-m);
  min-width: var(--bf-nav-side-width);
  transform: translate(calc(0px - var(--bf-nav-mobile-width)));
  animation-duration: var(--bf-animation-duration);
  animation-timing-function: var(--bf-timing-function);
  /* override min-width only for devices that support env() syntax */
  /* account for notch aka "busslomme" */
  min-width: calc(var(--bf-nav-side-width) + env(safe-area-inset-left));

  /* 8px between items + 4px extra in container for 12px total */
  padding-top: var(--bfs4);

  padding-right: env(safe-area-inset-right);
  padding-left: env(safe-area-inset-left);
}

.bf-nav-mobile.bf-nav-mobile-open {
  transform: translate(0);
  animation-name: navMobileSlideIn;
}

.bf-nav-mobile.bf-nav-mobile-close {
  animation-name: navMobileSlideOut;
}

@keyframes navMobileSlideIn {
  from {
    transform: translate(calc(0px - var(--bf-nav-mobile-width)));
  }
  to {
    transform: translate(0);
  }
}
@keyframes navMobileSlideOut {
  from {
    transform: translate(0);
  }
  to {
    transform: translate(calc(0px - var(--bf-nav-mobile-width)));
  }
}

@media (min-width: 960px) {
  .bf-nav-mobile {
    padding-right: 0;
    --bf-nav-mobile-width: 280px;
    width: var(--bf-nav-mobile-width);
    /* override max-width only for devices that support env() syntax */
    --bf-nav-mobile-width: calc(280px + env(safe-area-inset-left));
    width: var(--bf-nav-mobile-width);
  }
}

/* Overlay */
.bf-nav-mobile-overlay {
  position: fixed;
  top: var(--bf-nav-top-height);
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -99;
  background-color: var(--bfc-overlay);
  transition: all var(--bf-animation-duration) var(--bf-timing-function);
  opacity: 0;
}
.bf-nav-mobile-overlay.bf-nav-mobile-open {
  z-index: 9998;
  opacity: 1;
}

/* top bar */
.bf-nav-top {
  display: flex;
  background: var(--bfc-base-3);
  height: var(--bf-nav-top-height);
  position: sticky;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;

  flex-flow: row nowrap;
  justify-content: flex-start;
}

.bf-nav-top .bf-intility-logo {
  border-bottom: var(--bfl-border);
}

.bf-nav-top-appname {
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  font-size: var(--bf-font-size-l);
  color: var(--bfc-base-c-2);
  padding-left: var(--bfs16);
  border-bottom: var(--bfl-border);
  flex-grow: 2;
}
@media (min-width: 600px) {
  .bf-nav-top .bf-intility-logo {
    border-right: 1px solid var(--bfc-base-c-dimmed);
  }
}

.bf-nav-top-content {
  display: flex;
  justify-content: flex-end;
}

.bf-nav-top .bf-nav-profile {
  border-radius: 50%;
  width: 32px;
  height: 32px;
  border: 1px solid var(--bfc-base-c-dimmed);
}

.bf-nav-side {
  --bf-nav-side-item-height: 54px;
  --bf-nav-side-border-right: var(--bfl-border);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: var(--bf-nav-side-width);
  background-color: var(--bfc-base-3);
  color: var(--bfc-base-c);
  font-size: var(--bf-font-size-m);
  border-right: var(--bf-nav-side-border-right);
  transition: width var(--bf-nav-side-transition);
  display: flex;
  flex-direction: column;
}

.bf-nav-side-standalone {
  --bf-nav-side-border-right: none;
  box-shadow: var(--bf-shadow-right);
}

.bf-nav-side-appname {
  padding: 0 21px;
  line-height: var(--bfs32);
  font-size: var(--bf-font-size-s);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-right: var(--bf-nav-side-border-right);
  border-bottom: var(--bfl-border);
  color: var(--bfc-base-c-2);
  background-color: var(--bfc-base-2);
}

.bf-nav-side-content {
  width: var(--bf-nav-side-width);
  border-right: var(--bf-nav-side-border-right);
  overflow-y: auto;
  transition: width var(--bf-nav-side-transition);
  flex-grow: 1;
}

.bf-nav-side .bf-nav-side-collapse-button {
  transition: width var(--bf-nav-side-transition);
  cursor: pointer;
  display: block;
  background-color: var(--bfc-base-2);
  width: 100%;
  border-top: 1px solid var(--bfc-base-dimmed);
}
.bf-nav-side-collapse-button .bf-nav-side-item-icon {
  color: var(--bfc-base-c-theme);
}

.bf-nav-side-collapsed .bf-nav-side-appname {
  padding-left: 0;
  padding-right: 0;
  text-align: center;
}

/* make space to the left of top bar for the fixed Sidebar */
.bf-nav-side ~ .bf-nav-top {
  margin-left: var(--bf-nav-side-width);
  transition: margin var(--bf-nav-side-transition);
}

/* make space to the left of main content for the fixed Sidebar */
.bf-nav-side ~ main,
.bf-nav-side ~ .bf-main {
  margin-left: var(--bf-nav-side-width);
  transition: margin var(--bf-nav-side-transition);
  position: relative;
  z-index: 1;
}

/* reset default link styling when used as navigation wrappers */
.bf-nav > a,
.bf-nav-top-content > a,
.bf-nav-side-content > a,
.bf-nav-group > a,
.bf-nav-group-content > a,
.bf-nav-group-dropout-content > a {
  text-decoration: none;
  display: block;
  color: inherit;
  outline: none;
}
/* reset default button styling when used as navigation wrappers */
.bf-nav > button,
.bf-nav-top-content > button,
.bf-nav-side-content > button,
.bf-nav-group > button,
.bf-nav-group-content > button,
.bf-nav-group-dropout-content > button {
  color: inherit;
  outline: none;
  cursor: pointer;
  border: none;
  background-color: transparent;
  font-family: inherit;
  font-size: inherit;
  padding: 0;
  text-align: left;
  -webkit-appearance: none;
  height: 100%;
  margin: 0;
}

.bf-nav-top-content > button {
  height: var(--bf-nav-top-height);
  margin: 0;
}

.bf-nav-side .bf-nav-group > button,
.bf-nav-mobile .bf-nav-group > button {
  width: 100%;
}

/* Nav.Header */
.bf-nav-header {
  margin: var(--bfs8) var(--bfs24);
  color: var(--bfc-base-c-2);
  font-size: var(--bf-font-size-s);
}
.bf-nav-header:first-child {
  margin-top: var(--bfs4);
}
