.bf-topbar {
  display: flex;
  background: var(--bfc-base-3);
  height: var(--bf-topbar-height);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 2;

  flex-flow: row nowrap;
  justify-content: flex-start;
}

/* make space above content for the fixed TopBar */
.bf-topbar ~ main,
.bf-topbar ~ .bf-main {
  margin-top: var(--bf-topbar-height);
  position: relative;
  z-index: 1;
}
/* hide topbar logo when site has a sidebar */
.bf-sidebar ~ .bf-topbar .bf-intility-logo {
  display: none;
}

.bf-topbar .bf-intility-logo {
  border-right: 1px solid var(--bfc-base-c-dimmed);
  border-bottom: var(--bfl-border);
}
.bf-topbar .bf-intility-logo > a {
  padding-right: var(--bfs16);
}

.bf-topbar-appname-container {
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  font-size: var(--bf-font-size-l);
  color: var(--bfc-base-c-2);
  padding-left: var(--bfs16);
  border-bottom: var(--bfl-border);
  flex-grow: 2;
}

.bf-topbar-appname-container img {
  height: calc(var(--bf-topbar-height) - 2.2rem);
}

.bf-topbar-appname-container img:not(:only-child) {
  margin-right: var(--bfs8);
}

.bf-topbar-appname-container > :first-child {
  display: flex;
  align-items: center;
}

.bf-topbar-items-container {
  display: flex;
  justify-content: flex-end;
}

.bf-topbar-item {
  position: relative;
  border-left: var(--bfl-border);
  display: flex;
  align-items: center;
  color: var(--bfc-base-c);
  font-size: var(--bf-font-size-m);
  cursor: pointer;

  margin: 0;
  padding: var(--bfs8) var(--bfs16);
  box-shadow: inset 0 -1px var(--bfc-base-dimmed);
  justify-content: center;
  min-width: 56px;
  height: 100%;
}

.bf-topbar-item > div {
  display: flex;
  align-items: center;
}

.bf-topbar-item:last-child {
  border-right: none;
}

a:hover .bf-topbar-item {
  box-shadow: inset 0 -2px var(--bfc-base-c-theme);
  background-color: var(--bfc-theme-c-1);
  color: var(--bfc-base-c-theme);
}

.bf-topbar a:focus {
  outline: none;
}
a[data-focus-visible-added] .bf-topbar-item {
  background-color: var(--bfc-theme-c-2);
  box-shadow: inset 0 -2px var(--bfc-base-c-theme);
  color: var(--bfc-base-c-theme);
}

a:active .bf-topbar-item {
  background-color: var(--bfc-theme-c-2);
  color: var(--bfc-base-c-theme);
}

a[data-focus-visible-added] .bf-topbar-item:after {
  content: '';
  display: block;
  border: 5px solid transparent;
  position: absolute;
  bottom: 0;
  left: 50%;
  border-bottom-color: var(--bff-theme);
  transform: translateX(-50%);
}

a.active .bf-topbar-item,
a.active:hover .bf-topbar-item,
a.active[data-focus-visible-added] .bf-topbar-item {
  background-color: var(--bfc-base-1);
  box-shadow: inset 0 -2px var(--bfc-base-c-theme);
  cursor: default;
  color: var(--bfc-base-c-theme);
}

a:active .bf-topbar-item:after,
a.active[data-focus-visible-added] .bf-topbar-item:after {
  content: '';
  border: 0;
}

.bf-topbar-item-icon-left .svg-inline--fa,
.bf-topbar-item-icon-left .fa {
  margin-right: var(--bfs8);
}

.bf-topbar-item-icon-right .svg-inline--fa,
.bf-topbar-item-icon-right .fa {
  margin-left: var(--bfs8);
}

.bf-topbar-item .bf-topbar-item-link {
  display: flex;
  padding: var(--bfs8) var(--bfs16);
  align-items: center;
}

/* icon */
.bf-topbar-item .svg-inline--fa {
  font-size: var(--bf-font-size-l);
}
.bf-topbar-group .bf-topbar-item .svg-inline--fa {
  font-size: var(--bf-font-size-s);
  color: var(--bfc-base-c-2);
}
.bf-topbar-group a:hover .bf-topbar-item .svg-inline--fa,
.bf-topbar-group a:focus .bf-topbar-item .svg-inline--fa {
  color: inherit;
}
.bf-topbar-item svg:only-child {
  margin: 0;
}

.bf-topbar-group-container {
  display: flex;
  align-items: stretch;
  min-width: 56px;
}

a:not([data-focus-visible-added]):not(:active):not(:hover)
  .bf-topbar-group-active {
  background-color: var(--bfc-base-2);
  color: var(--bfc-base-c-theme);
  box-shadow: inset 0 -1px var(--bfc-base-dimmed);
}

.bf-topbar-group-container
  a[data-focus-visible-added]
  .bf-topbar-group-active:after,
.bf-topbar-group-container a:active .bf-topbar-group-active:after,
.bf-topbar-group a[data-focus-visible-added] .bf-topbar-item:after,
.bf-topbar-group a:active .bf-topbar-item:after {
  display: none;
}

.bf-topbar-group {
  font-size: var(--bf-font-size-s);
  min-width: 200px;
  background-color: var(--bfc-base-3);
  border-radius: var(--bfl-border-radius);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
}
.bf-topbar-group.tippy-box {
  color: inherit; /* override tippy default css */
}

.bf-topbar-group .tippy-content {
  padding: var(--bfs8) 0;
}

.bf-topbar-group a .bf-topbar-item {
  justify-content: flex-start;
  font-size: var(--bf-font-size-s);
  border: 0;
  box-shadow: none;
  padding: var(--bfs12) 23px var(--bfs12) var(--bfs24);
  cursor: pointer;
  width: 100%;
  border: none;
}

.bf-topbar-group a:hover .bf-topbar-item,
.bf-topbar-group a[data-focus-visible-added] > .bf-topbar-item {
  background-color: initial;
}

.bf-topbar-group a:active .bf-topbar-item {
  color: var(--bfc-theme-3);
}

.bf-topbar-group hr {
  padding: 0;
  margin: var(--bfs8) 0;
  border: var(--bfl-border);
  border-bottom: none;
}

.bf-topbar-group .tippy-arrow {
  color: var(--bfc-base-3);
}

.bf-topbar-group a.active .bf-topbar-item {
  color: var(--bfc-base-c-theme);
  background-color: transparent;
  box-shadow: none;
}

.bf-topbar-group a.active .bf-topbar-selected-icon,
.bf-topbar-group a.active .bf-topbar-unselected-icon {
  color: var(--bfc-base-c-theme);
}

/* display only selected or unselected icon based on .active class */
.bf-topbar-group a:not(.active) .bf-topbar-selected-icon,
.bf-topbar-group a.active .bf-topbar-unselected-icon {
  display: none;
}

.bf-topbar-group .bf-topbar-selected-icon,
.bf-topbar-group .bf-topbar-unselected-icon {
  margin-left: auto;
  margin-right: 0;
  color: var(--bfc-base-dimmed);
  font-size: var(--bf-font-size-s);
}

.bf-topbar-group a:hover .bf-topbar-selected-icon,
.bf-topbar-group a[data-focus-visible-added] .bf-topbar-selected-icon,
.bf-topbar-group a:hover .bf-topbar-unselected-icon,
.bf-topbar-group a[data-focus-visible-added] .bf-topbar-unselected-icon {
  color: var(--bfc-base-c-theme);
}

.bf-topbar-group a:active .bf-topbar-selected-icon,
.bf-topbar-group a:active .bf-topbar-unselected-icon {
  color: var(--bfc-theme-3);
}

.bf-topbar-group a.active:active .bf-topbar-item,
.bf-topbar-group a.active:active .bf-topbar-selected-icon,
.bf-topbar-group a.active:active .bf-topbar-unselected-icon {
  color: var(--bfc-base-c-theme);
}

.bf-topbar-group-container
  .bf-topbar-group[data-placement^='bottom']
  > .tippy-arrow {
  position: absolute;
  left: auto;
  transform: none;
  right: 20px;
}

.bf-topbar-items-container a,
.bf-topbar-items-container a:link,
.bf-topbar-items-container a:visited,
.bf-topbar-items-container a:hover,
.bf-topbar-items-container a:active,
.bf-topbar-items-container a[data-focus-visible-added] {
  text-decoration: none;
  display: block;
  box-shadow: none;
  margin: 0;
  padding: 0;
  color: inherit;
  background-color: transparent;
}
