@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,700&display=swap');

:root {
  /* ~1px at 14px font-size */
  --bf-underline-offset: 0.0714em;
}

body {
  font-family: 'Open Sans', sans-serif;
  line-height: 1.5;
  margin: 0;
  /* Prevent font scaling in landscape while allowing user zoom */
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
  /* Eirik's mac hack */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body,
.bf-options {
  display: flow-root;
  color: var(--bfc-base-c);
  background-color: var(--bfc-base);
  min-height: 100vh;
}

/* padding for devices with a 'notch' a.k.a. 'busslomme' */
.bf-nav ~ main,
.bf-main,
.bf-notch {
  padding-right: env(safe-area-inset-right);
  padding-left: env(safe-area-inset-left);
}
/* a negative margin enables content inside main to stretch full width */
.bf-negative-notch {
  margin-right: calc(0px - env(safe-area-inset-right));
  margin-left: calc(0px - env(safe-area-inset-left));
}

/* Scrollbar */
@media (min-width: 600px) and (min-height: 600px) {
  .bf-scrollbar::-webkit-scrollbar,
  .bf-scrollbar-small::-webkit-scrollbar {
    height: 18px;
    width: 18px;
    background-color: transparent;
  }
  .bf-scrollbar::-webkit-scrollbar-corner,
  .bf-scrollbar-small::-webkit-scrollbar-corner {
    background-color: transparent;
  }
  .bf-scrollbar::-webkit-scrollbar-thumb,
  .bf-scrollbar-small::-webkit-scrollbar-thumb {
    height: 30px;
    width: 30px;
    border: 4px solid transparent;
    background-color: var(--bfc-base-dimmed);
    background-clip: padding-box;
    border-radius: 10px;
    -webkit-border-radius: 10px;
  }
  body.bf-scrollbar::-webkit-scrollbar,
  body.bf-scrollbar::-webkit-scrollbar-corner {
    background-color: var(--bfc-base-3);
  }
  /* small firefox scrollbar */
  .bf-scrollbar-small {
    scrollbar-color: hsla(var(--bfc-base-c-inverted-hsl), 0.2) transparent;
    scrollbar-width: thin;
  }
  /* small chrome scrollbar */
  .bf-scrollbar-small::-webkit-scrollbar {
    width: 14px;
    height: 14px;
  }
}

/* https://css-tricks.com/snippets/css/prevent-long-urls-from-breaking-out-of-container/ */
.bf-break-word {
  /* These are technically the same, but use both */
  overflow-wrap: break-word;
  word-wrap: break-word;

  -ms-word-break: break-all;
  /* This is the dangerous one in WebKit, as it breaks things wherever */
  word-break: break-all;
  /* Instead use this non-standard one: */
  word-break: break-word;

  /* Adds a hyphen where the word breaks, if supported (No Blink) */
  -ms-hyphens: auto;
  -moz-hyphens: auto;
  -webkit-hyphens: auto;
  hyphens: auto;
}

.bf-content p,
.bf-p {
  font-size: var(--bf-font-size-m);
  margin: var(--bfs16) 0;
}

/* link, title-link, and neutral-link styles */
.bf-link,
.bf-content a,
.bf-link:visited,
.bf-content a:visited,
.bf-link:link,
.bf-content a:link {
  cursor: pointer;
  color: var(--bff-theme);
  text-decoration: underline;
  text-underline-offset: var(--bf-underline-offset);
}
button.bf-link,
button.bf-title-link {
  border: none;
  background: inherit;
  padding: 0;
  font: inherit;
}
.bf-link:hover,
.bf-content a:hover,
.bf-link:active,
.bf-content a:active {
  text-decoration: underline dashed;
}
.bf-link:focus,
.bf-title-link:focus,
.bf-content a:focus {
  outline: none;
}
.bf-link[data-focus-visible-added],
.bf-title-link[data-focus-visible-added],
.bf-neutral-link[data-focus-visible-added],
.bf-content a[data-focus-visible-added],
.bf-content .bf-title-link[data-focus-visible-added] {
  outline: dashed currentColor 1px;
  -moz-outline-radius: var(--bfl-border-radius);
  border-radius: var(--bfl-border-radius);
}
.bf-title-link,
.bf-content .bf-title-link,
.bf-title-link:visited,
.bf-content .bf-title-link:visited,
.bf-title-link:link,
.bf-content .bf-title-link:link {
  color: var(--bff-theme);
  text-decoration: none;
  cursor: pointer;
}
.bf-title-link:hover,
.bf-content .bf-title-link:hover,
.bf-title-link:active,
.bf-content .bf-title-link:active {
  text-decoration: underline;
  text-underline-offset: var(--bf-underline-offset);
}
.bf-neutral-link,
.bf-neutral-link:visited,
.bf-neutral-link:link,
.bf-neutral-link:hover,
.bf-content .bf-neutral-link,
.bf-content .bf-neutral-link:visited,
.bf-content .bf-neutral-link:link,
.bf-content .bf-neutral-link:hover {
  color: inherit;
  text-decoration: none;
}
button.bf-neutral-link,
.bf-content button.bf-neutral-link {
  text-align: inherit;
  background: none;
  border: none;
  font: inherit;
  cursor: pointer;
  padding: 0;
  outline: none;
}
button.bf-neutral-link[data-focus-visible-added] {
  outline: 1px dashed var(--bff-theme);
  -moz-outline-radius: var(--bfl-border-radius);
}
.bf-neutral-link:hover .bf-neutral-link-text,
.bf-content .bf-neutral-link:hover .bf-neutral-link-text {
  text-decoration: underline;
  text-underline-offset: var(--bf-underline-offset);
}

/* headings */
.bf-content h1,
.bf-h1 {
  font-size: var(--bf-font-size-h1);
  font-weight: 400;
  line-height: 1.3;
  margin-top: var(--bfs32);
  margin-bottom: var(--bfs12);
}
.bf-content h2,
.bf-h2 {
  font-size: var(--bf-font-size-h2);
  font-weight: 400;
  margin-top: var(--bfs32);
  margin-bottom: var(--bfs8);
}
.bf-content h3,
.bf-h3 {
  font-size: var(--bf-font-size-h3);
  font-weight: 400;
  margin-top: var(--bfs32);
  margin-bottom: var(--bfs8);
}
.bf-content h4,
.bf-h4 {
  font-size: var(--bf-font-size-h4);
  font-weight: 400;
  margin-top: var(--bfs24);
  margin-bottom: var(--bfs8);
}
.bf-content h5,
.bf-h5 {
  font-size: var(--bf-font-size-h5);
  font-weight: 600;
  margin-top: var(--bfs16);
  margin-bottom: var(--bfs16);
}

.bf-content small,
.bf-content .bf-small,
.bf-small {
  font-size: var(--bf-font-size-s);
}
.bf-content .bf-large,
.bf-large {
  font-size: var(--bf-font-size-l);
}
.bf-content code,
.bf-code {
  font-family: Consolas, monospace;
  background-color: hsla(var(--bfc-base-c-inverted-hsl), 0.1);
  display: inline-block;
  padding: 0 0.2rem;
  margin: 0 0.1rem;
  border-radius: var(--bfl-border-radius);
}
.bf-content a code,
a .bf-code {
  color: var(--bfc-theme);
  cursor: pointer;
  text-decoration: underline;
}
.bf-content a:hover code,
a:hover .bf-code {
  text-decoration: underline dashed;
}
.bf-content a:hover code,
.bf-content a[data-focus-visible-added] code,
.bf-content a:active code,
a:hover .bf-code,
a[data-focus-visible-added] .bf-code,
a:active .bf-code {
  background-color: transparent;
  color: var(--bfc-theme);
}
.bf-em {
  font-style: italic;
}
.bf-content strong,
.bf-content b,
.bf-strong {
  font-weight: 600;
}

/* list styling */
.bf-content ul,
.bf-content ol,
.bf-ul,
.bf-ol {
  font-size: var(--bf-font-size-m);
  padding-left: 2rem;
}
.bf-content ul ::marker,
.bf-content ol ::marker,
.bf-ul ::marker,
.bf-ol ::marker {
  color: var(--bfc-theme);
}
.bf-content li,
.bf-li {
  font-size: var(--bf-font-size-m);
  margin: var(--bfs12) 0;
}

/* used by Nav (and deprecated TopBar and SideBar) */
.bf-intility-logo[class] > a {
  height: var(--bf-nav-top-height);
  line-height: var(--bf-nav-top-height);
  border-bottom: var(--bfl-border);
  display: block;
  line-height: 1;
  cursor: pointer;
}
.bf-intility-logo[class] > a:focus {
  outline: none;
}
.bf-intility-logo[class] > a:hover,
.bf-intility-logo[class] > a[data-focus-visible-added] {
  background-color: transparent;
  outline: initial;
}
.bf-intility-logo > a:hover svg {
  fill: var(--bfc-theme-2);
}
.bf-intility-logo > a[data-focus-visible-added] svg {
  fill: var(--bfc-theme-3);
}
.bf-intility-logo svg {
  margin: 17px 19px 0 19px;
  height: var(--bfs24);
  fill: var(--bff-theme);
}
