:root {
  --bf-drawer-width: 480px;
}

/* Drawer */
.bf-drawer {
  position: fixed;
  top: var(--bf-nav-top-height);
  right: 0;
  bottom: 0;
  z-index: 99999;
  outline: none;
  overflow: auto;
  padding: var(--bfl-modal-padding);
  width: var(--bf-drawer-width);
  background: var(--bfc-base-2);
  transform: translate(calc(0px + 100%));
  transition: transform var(--bf-animation-duration) var(--bf-timing-function);
}
.bf-drawer.bf-drawer-open {
  transform: translate(0);
}

/* Header */
.bf-drawer-header {
  font-size: var(--bf-font-size-xxxl);
}

/* Overlay */
.bf-drawer-overlay,
.bf-drawer-force-overlay {
  position: fixed;
  top: var(--bf-nav-top-height);
  right: 0;
  bottom: 0;
  left: 0;
  background-color: var(--bfc-overlay);
  transition: opacity var(--bf-animation-duration) var(--bf-timing-function),
    visibility var(--bf-animation-duration);
  opacity: 0;
  visibility: hidden;
}
.bf-drawer-overlay.bf-drawer-open,
.bf-drawer-force-overlay.bf-drawer-open {
  z-index: 99998;
  opacity: 1;
  visibility: visible;
}

/* Small */
@media (max-width: 599.9px), (max-height: 599.9px) {
  .bf-drawer {
    width: 100vw;
  }
  .bf-drawer-overlay {
    display: none;
  }
}

@media (min-width: 1280px) {
  :root {
    --bf-drawer-width: 620px;
  }
}

/* Large */
@media (min-width: 1280px) {
  .bf-drawer:not(.bf-overlay-visible) {
    border-left: 1px solid var(--bfc-base-dimmed);
  }
  .bf-drawer-overlay:not(.bf-drawer-force-overlay) {
    display: none;
  }
  .bf-drawer-padding {
    padding-right: calc(var(--bf-drawer-width) + 24px);
  }
}
