.bf-nav-expand-icon {
  position: absolute;
  right: var(--bfs16);
}

.bf-nav-group-content {
  background-color: var(--bfc-base-2);
  padding: var(--bfs12) 0;
}

.bf-nav-group-content .bf-nav-group-item-circle-wrapper,
.bf-nav-group-dropout .bf-nav-group-item-circle-wrapper {
  display: inline;
  position: absolute;
  right: var(--bfs16);
}

.bf-nav-group-content a.active .bf-nav-group-item-circle,
.bf-nav-group-dropout a.active .bf-nav-group-item-circle {
  display: none;
}

.bf-nav-group-content a.active .bf-nav-group-item-dot-circle,
.bf-nav-group-dropout a.active .bf-nav-group-item-dot-circle {
  display: inline;
}

.bf-nav-group-content .bf-nav-item,
.bf-nav-group-content a > .bf-nav-item,
.bf-nav-group-dropout .bf-nav-item,
.bf-nav-group-dropout a > .bf-nav-item {
  font-size: var(--bf-font-size-m);
  line-height: 38px;
  padding-right: var(--bfs32);
}

.bf-nav-group-content > hr {
  padding: 0;
  margin: 0;
  border: var(--bfl-border);
  border-bottom: none;
  margin: var(--bfs12) 0;
}

/* sidebar popup when collapsed */
.bf-nav-group .bf-nav-group-dropout-visible {
  background-color: var(--bfc-base-2);
}

/* Nav.Group dropout */
.bf-nav-group-dropout {
  min-width: 230px;
  color: var(--bfc-base-c);
  background-color: var(--bfc-base-3);
  border-radius: var(--bfl-border-radius);
  padding-bottom: var(--bfs12);
}

.bf-nav-group-dropout .tippy-content,
.bf-nav-group-dropout.bf-dropdown {
  padding: 0;
}

.bf-nav-group-dropout[data-placement^='right'] > .tippy-arrow::before {
  border-right-color: var(--bfc-base-3);
}

.bf-nav-group-dropout[data-placement^='bottom'] > .tippy-arrow::before {
  border-bottom-color: var(--bfc-base-3);
}

.bf-nav .bf-nav-group-dropout .bf-nav-item {
  font-size: var(--bf-font-size-m);
  padding-left: var(--bfs24);
  line-height: 38px;
  cursor: pointer;
}

.bf-nav-group-dropout a:hover,
.bf-nav-group-dropout a[data-focus-visible-added],
.bf-nav-group-dropout a:hover > .bf-nav-item,
.bf-nav-group-dropout a[data-focus-visible-added] > .bf-nav-item {
  background-color: initial;
}

.bf-nav-group-dropout-title {
  border-bottom: 1px solid var(--bff-dimmed);
  color: var(--bff-wcag);
  padding-left: 24px;
  line-height: var(--bf-nav-side-item-height);
  font-size: var(--bf-font-size-s);
  font-style: italic;
  margin-bottom: var(--bfs12);
}

.bf-nav-group-dropout hr {
  padding: 0;
  margin: var(--bfs12) 0;
  border: var(--bfl-border);
  border-bottom: none;
  margin-top: var(--bfs8);
}

/* Nav.Items within Nav.Group */
/* selected, hover */
.bf-nav-group-content a.active > .bf-nav-item,
.bf-nav-group-content button.active > .bf-nav-item,
.bf-nav-group-content a.active:hover > .bf-nav-item,
.bf-nav-group-content button.active:hover > .bf-nav-item {
  font-weight: normal;
  color: var(--bft-theme);
}
.bf-nav-top .bf-nav-group-dropout .bf-nav-item {
  border: 1px solid transparent;
  padding: 0 0 0 var(--bfs24);
  font-size: var(--bf-font-size-m);
  justify-content: flex-start;
}
.bf-nav-group-dropout a:hover .bf-nav-item {
  color: var(--bfc-base-c-theme);
}
.bf-nav .bf-nav-group-dropout .bf-nav-item-with-icon {
  padding-left: 0 !important;
}
.bf-nav-group-content .bf-nav-item-icon,
.bf-nav-group-dropout .bf-nav-item-icon {
  color: var(--bfc-base-c-2);
  margin: auto 8px auto 21px;
  width: auto;
}

/* Nav top */
.bf-nav-top .bf-nav-group-button > .bf-nav-item {
  /* safari doesnt like automatic button heights, explicitly define height */
  height: var(--bf-nav-top-height);
}
.bf-nav-top .bf-nav-group-dropout-content > section {
  margin: 20px 0;
}
.bf-nav-top .bf-nav-group-dropout-content .bf-checkbox-label {
  padding-top: 5px;
  padding-bottom: 5px;
}
.bf-nav-top
  .bf-nav-group-dropout-content
  > section
  > :last-child
  .bf-checkbox-label {
  margin-bottom: -6px;
}
.bf-nav-top .bf-nav-group-dropout-content hr {
  margin: 0;
}
.bf-nav-top .bf-nav-group-dropout-content .bf-nav-item {
  padding-left: 10px !important; /* eric noob */
}
.bf-nav-top .bf-nav-group-dropout-content .bf-nav-item > svg {
  margin: 0;
  width: 42px;
}

/* #region mobile nav */
.bf-nav-mobile .bf-nav-group > a > .bf-nav-item,
.bf-nav-mobile .bf-nav-group > button > .bf-nav-item {
  margin-bottom: 0;
}
.bf-nav-mobile .bf-nav-group-content {
  border-bottom-right-radius: var(--bfl-border-radius);
  border-bottom-left-radius: var(--bfl-border-radius);
  border: 1px solid var(--bfc-base-dimmed);
  border-top: none;
  background-color: var(--bfc-base-3);
  margin: var(--bfs8);
  margin-top: 0;
  padding: var(--bfs12);
  display: grid;
  gap: var(--bfs8);
  grid-template-columns: repeat(auto-fill, minmax(min(112px, 100%), 1fr));
}
.bf-nav-mobile .bf-nav-group-button-expanded .bf-nav-item {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.bf-nav-mobile .bf-nav-group-item-circle-wrapper {
  display: none;
}
.bf-nav-mobile .bf-nav-group-content > a,
.bf-nav-mobile .bf-nav-group-content > button {
  text-align: center;
}
.bf-nav-mobile .bf-nav-group-content .bf-nav-item {
  margin: 0;
  background: var(--bfc-base-3);
  color: var(--bfc-theme);
  padding: var(--bfs8);
  line-height: normal;
  border: 1px solid var(--bfc-base-c-wcag);
  font-size: var(--bf-font-size-m);
  font-family: inherit;
  cursor: pointer;
  white-space: normal;
  display: grid;
  align-items: center;
  height: 100%;
}
.bf-nav-mobile .bf-nav-group-content > *:hover .bf-nav-item {
  background-color: var(--bfc-base-2);
}
.bf-nav-mobile
  .bf-nav-group-content
  > *[data-focus-visible-added]
  .bf-nav-item {
  border: 1px solid var(--bfc-base-c-wcag);
  outline: 1px dashed var(--bff-theme);
  outline-offset: 1px;
}
.bf-nav-mobile .bf-nav-group-content > *:active .bf-nav-item {
  background-color: var(--bfc-base);
}
/* #endregion */

/* checkbox inside group */
.bf-nav-group-content .bf-checkbox-label,
.bf-nav-group-dropout-content .bf-checkbox-label,
.bf-dropdown .bf-nav-group-dropout-content .bf-checkbox-label {
  margin-left: 10px;
  padding-left: 42px;
}
