.bf-dropdown {
  background: var(--bfc-base-3);
  color: var(--bfc-base-c);
  border: 1px solid transparent;
  border-radius: 3px;
  min-width: 200px;
  padding: var(--bfs12);
  box-shadow: var(--bf-shadow);
  font-size: var(--bf-font-size-m);
}
.bf-dropdown > .tippy-arrow {
  color: var(--bfc-base-3);
}
/* Variant: Border */
.bf-dropdown.bf-dropdown-border {
  border: 1px solid var(--bfc-base-c-wcag);
  box-shadow: unset;
}
.bf-dropdown-nopadding,
.bf-dropdown-nopadding .tippy-content {
  padding: 0;
}
.bf-dropdown.tippy-box {
  line-height: unset;
}

/* Dropdown with Checkbox */
.bf-dropdown .bf-checkbox-label,
.bf-content .bf-dropdown .bf-checkbox-label {
  display: block;
  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 36px;
}

.bf-dropdown .bf-checkbox-icon {
  left: var(--bfs12);
}
