/* INPUT */
.bf-datepicker {
  height: 40px;
}

.bf-datepicker:hover {
  cursor: pointer;
}

/* DATEPICKER */
.react-datepicker {
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  background: var(--bfc-base-3);
  color: var(--bfc-base-c-1);
  border: 1px solid var(--bfc-base-c-dimmed);
  border-radius: 3px;
  display: block;
  position: relative;
  font-size: var(--bf-font-size-m);
  min-width: 243px;
}

/* When showTimeSelect is true */
.bf-datepicker-icon-container.bf-show-time-select .react-datepicker {
  display: flex;
  min-width: 329px;
}

/* Remove inline-block from default */
.react-datepicker-wrapper {
  display: block;
  width: 100%;
}

/* MONTH / YEAR DROPDOWN BUTTON */
.react-datepicker__year-dropdown-container--select,
.react-datepicker__month-dropdown-container--select,
.react-datepicker__month-year-dropdown-container--select,
.react-datepicker__year-dropdown-container--scroll,
.react-datepicker__month-dropdown-container--scroll,
.react-datepicker__month-year-dropdown-container--scroll {
  display: inline-block;
  margin: 0 2px;
}

/* POPPER */
.react-datepicker-popper {
  z-index: 1;
}
.react-datepicker-popper[data-placement^='bottom'] {
  margin-top: 14px;
}
.react-datepicker-popper[data-placement^='top'] {
  margin-bottom: 14px;
}
.react-datepicker-popper[data-placement^='right'] {
  margin-left: 14px;
}
.react-datepicker-popper[data-placement^='left'] {
  margin-right: 14px;
}
.react-datepicker-popper[data-placement^='bottom'] .react-datepicker__triangle,
.react-datepicker-popper[data-placement^='top'] .react-datepicker__triangle {
  margin-left: -8px;
  position: absolute;
  box-sizing: content-box;
  position: absolute;
  border: 8px solid transparent;
  height: 0;
  width: 1px;
}
.react-datepicker-popper[data-placement^='bottom']
  .react-datepicker__triangle::before,
.react-datepicker-popper[data-placement^='top']
  .react-datepicker__triangle::before {
  box-sizing: content-box;
  position: absolute;
  border: 8px solid transparent;
  height: 0;
  width: 1px;
  content: '';
  z-index: -1;
  border-width: 8px;
  left: -8px;
}
.react-datepicker-popper[data-placement^='bottom'] .react-datepicker__triangle {
  top: 0;
  margin-top: -8px;
  border-top: none;
  border-bottom-color: var(--bfc-base-3);
}
.react-datepicker-popper[data-placement^='bottom']
  .react-datepicker__triangle::before {
  border-top: none;
  top: -1px;
  border-bottom-color: var(--bfc-base-3);
}
.react-datepicker-popper[data-placement^='top'] .react-datepicker__triangle {
  bottom: 0;
  margin-bottom: -8px;
  border-bottom: none;
  border-top-color: var(--bfc-base-3);
}
.react-datepicker-popper[data-placement^='top']
  .react-datepicker__triangle::before {
  border-bottom: none;
  bottom: -1px;
  border-top-color: var(--bfc-base-3);
}

/* HEADER */
.react-datepicker__header,
.react-datepicker__current-month {
  text-align: center;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
  padding-top: 8px;
  position: relative;
  background: var(--bfc-base-3);
  font-weight: normal;
  border-bottom: 1px solid var(--bfc-base-c-dimmed);
}
.react-datepicker__day-names {
  margin-top: 6px;
  white-space: nowrap;
}
.react-datepicker__header__dropdown {
  padding: 4px 0;
}

/* TEXT */
.react-datepicker__header,
.react-datepicker__current-month,
.react-datepicker__day-name,
.react-datepicker__day {
  color: var(--bfc-base-c-1);
}

.react-datepicker__day,
.react-datepicker__month-text,
.react-datepicker__quarter-text,
.react-datepicker__year-text,
.react-datepicker__year-read-view,
.react-datepicker__month-read-view,
.react-datepicker__month-option,
.react-datepicker__year-option {
  cursor: pointer;
}

.react-datepicker__day-name,
.react-datepicker__day {
  display: inline-block;
  width: 1.7rem;
  line-height: 1.7rem;
  text-align: center;
  margin: 0.166rem;
}

/* DAY */
.react-datepicker__day {
  border: 1px dashed transparent;
  border-radius: 3px;
}

/* DAY TODAY */
.react-datepicker__day.react-datepicker__day--today {
  font-weight: 700;
}

/* DAY HOVER */
.react-datepicker__day:hover {
  background-color: var(--bfc-base-2);
  color: var(--bfc-base-c-theme);
}

/* DAY SELECTED */
.react-datepicker__week .react-datepicker__day--selected {
  background: var(--bfc-theme-1);
  color: var(--bfc-base-3);
  outline: none;
}

/* DAY DISABLED */
.react-datepicker__day--outside-month {
  color: var(--bfc-base-c-2);
}

/* DAY DISABLED WHEN USING MINDATE OR MAXDATE */
.react-datepicker__day--disabled,
.react-datepicker__day--disabled:hover {
  color: var(--bfc-base-disabled);
  background-color: transparent;
  cursor: default;
}

/* SELECTING DAY WITH KEYBOARD */
.react-datepicker__week
  .react-datepicker__day--keyboard-selected[data-focus-visible-added] {
  border: 1px dashed var(--bfc-theme-1);
  outline: none;
}

/* SELECTED + FOCUS */
.react-datepicker__week
  .react-datepicker__day--selected[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-theme-1);
  outline-offset: 1px;
}

/* CLEARABLE DATE BUTTON */
.bf-datepicker-clearable.bf-datepicker-icon-left
  .react-datepicker__input-container
  .bf-datepicker {
  padding-right: var(--bfs40);
}

.bf-datepicker-clearable.bf-datepicker-icon-right
  .react-datepicker__input-container
  .bf-datepicker {
  padding-right: 70px;
}

.bf-datepicker.clearable.bf .react-datepicker__input-container {
  display: flex;
  position: relative;
}
.react-datepicker__close-icon {
  position: absolute;
  cursor: pointer;
  background: transparent;
  border: 0;
  outline: 0;
  padding: 0;
  color: var(--bfc-base-c-2);
  height: 100%;
  width: var(--bfs32);
  height: var(--bfs32);
  margin: var(--bfs4);
  padding: var(--bfs4) 0;
  font-size: var(--bf-font-size-xxl);
}

.bf-datepicker-icon-left .react-datepicker__close-icon {
  right: 0;
}

.bf-datepicker-icon-right .react-datepicker__close-icon {
  right: var(--bfs32);
}

.react-datepicker__close-icon:hover {
  color: var(--bfc-base-c-1);
}

.react-datepicker__close-icon::after {
  content: '\00d7';
}

/* DATES CONTAINER */
.react-datepicker__month {
  margin: 0.4rem;
  text-align: center;
}

/* DROPDOWN TITLES */
.react-datepicker__month-dropdown-container,
.react-datepicker__year-dropdown-container {
  font-size: var(--bf-font-size-m);
}

.bf-datepicker-container .react-datepicker__year-dropdown-container--scroll {
  margin: 1px 0px 0px 0px;
}

.react-datepicker__month-read-view--selected-month,
.react-datepicker__year-read-view--selected-year {
  padding: 4px 10px;
  border: 1px solid transparent;
  border-radius: 3px;
}

/* DROPDOWN TITLES HOVER */
.react-datepicker__month-read-view--selected-month:hover,
.react-datepicker__year-read-view--selected-year:hover {
  background-color: var(--bfc-base-2);
  color: var(--bfc-base-c-theme);
}

/* DROPDOWN MENUS */
.react-datepicker__month-dropdown,
.react-datepicker__year-dropdown {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 16px 0px;
  align-content: center;
  justify-content: center;
  background: var(--bfc-base-3);
  color: var(--bfc-base-c-1);
  padding: 15px 4px;
  font-size: var(--bf-font-size-m);
  position: absolute;
  top: 45px;
  width: calc(100% + 2px);
  left: -1px;
  height: 292px;
  border: 1px solid var(--bfc-base-c-dimmed);
  border-radius: 3px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

/* MONTH + YEAR OPTIONS */
.bf-datepicker-container .react-datepicker__month-option,
.bf-datepicker-container .react-datepicker__year-option {
  display: inline-block;
  width: 65px;
  line-height: 38px;
  height: 38px;
  border: 1px solid transparent;
  border-radius: 3px;
  margin: 6px 5px;
  vertical-align: bottom;
  border-radius: 3px;
}

/* DROPDOWN MENUS OPTIONS HOVER */
.react-datepicker__month-option:hover,
.react-datepicker__year-option:hover {
  background: var(--bfc-base-2);
  color: var(--bfc-theme-1);
}

/* SELECTED MONTH / YEAR */
.react-datepicker__month-option--selected_month,
.react-datepicker__year-option--selected_year {
  background: var(--bfc-theme-1);
  color: var(--bfc-base-3);
}

/* YEAR DROPDOWN SCROLL ARROW */
.react-datepicker__year-option:nth-child(12) {
  display: none;
}

/* NAVIGATION */
.react-datepicker__navigation {
  background: none;
  cursor: pointer;
  position: absolute;
  top: 16px;
  padding: 0;
  border: 0.45rem solid transparent;
  z-index: 1;
  height: 10px;
  width: 10px;
  text-indent: -999em;
  overflow: hidden;
  outline: none;
}
.react-datepicker__navigation--previous {
  left: 10px;
  border-right-color: var(--bfc-base-c-1);
  border-left-width: 0;
}
.react-datepicker__navigation--previous:hover {
  border-right-color: var(--bfc-theme);
}
.react-datepicker__navigation--next {
  right: 10px;
  border-left-color: var(--bfc-base-c-1);
  border-right-width: 0;
}
.react-datepicker__navigation--next:hover {
  border-left-color: var(--bfc-theme-1);
}
.react-datepicker__navigation--previous[data-focus-visible-added],
.react-datepicker__navigation--next[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-theme);
  outline-offset: 2px;
}
/* When showTimeSelect is true */
.react-datepicker__navigation--next--with-time:not(.react-datepicker__navigation--next--with-today-button) {
  right: 95px;
}

/* Years navigation arrows */
.react-datepicker__navigation--years {
  position: relative;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.bf-datepicker-container .react-datepicker__navigation--years-previous {
  top: 15px;
  padding: 0;
  border-radius: 0;
  border-top-color: var(--bfc-base-c-1);
}
.bf-datepicker-container .react-datepicker__navigation--years-upcoming {
  top: 7px;
  padding: 0;
  border-radius: 0;
  border-bottom-color: var(--bfc-base-c-1);
}
.bf-datepicker-container
  .react-datepicker__year-option:hover
  .react-datepicker__navigation--years-upcoming {
  background: none;
  box-shadow: none;
  border-bottom-color: var(--bfc-base-c-theme);
}
.bf-datepicker-container
  .react-datepicker__year-option:hover
  .react-datepicker__navigation--years-previous {
  background: none;
  box-shadow: none;
  border-top-color: var(--bfc-base-c-theme);
}
.react-datepicker__month-read-view,
.react-datepicker__year-read-view {
  visibility: visible !important;
}
.react-datepicker__month-read-view--down-arrow,
.react-datepicker__year-read-view--down-arrow {
  display: none;
}
.react-datepicker__current-month {
  display: none;
}

/* REMOVING ICON FROM SELECTED MONTH OPTION */
.react-datepicker__month-option--selected,
.react-datepicker__year-option--selected {
  display: none;
}

/* DISABLED */
/* also see bifrost-form.css */
.bf-datepicker-disabled .bf-datepicker,
.bf-datepicker-disabled .bf-datepicker:hover,
.bf-datepicker-disabled .bf-datepicker-icon-container .bf-datepicker-icon {
  background: var(--bfc-base-dimmed);
  color: var(--bfc-base-c-2);
  cursor: default;
  border-bottom-color: transparent;
}

/* ICON */
.bf-datepicker-icon-container {
  position: relative;
  display: flex;
}
.bf-datepicker-icon-container .bf-datepicker-icon {
  color: var(--bfc-base-c-2);
  position: absolute;
  height: var(--bfs32);
  width: var(--bfs32);
  margin: var(--bfs4);
  padding: var(--bfs4) 0;
  text-align: center;
  font-size: var(--bf-font-size-l);
}
.bf-datepicker-icon-left .bf-datepicker-icon {
  right: auto;
  left: 0;
}
.bf-datepicker-icon-right .bf-datepicker-icon {
  left: auto;
  right: 0;
}

.bf-datepicker-icon-right .bf-datepicker {
  /* using 2.375rem because we don't have CSS variable for it */
  padding-right: 2.375rem;
}

.bf-datepicker-icon-left .bf-datepicker {
  /* using 2.375rem because we don't have CSS variable for it */
  padding-left: 2.375rem;
}

.bf-datepicker-success .bf-state-icon {
  color: var(--bfc-base-c-success);
}
.bf-datepicker-warning .bf-state-icon {
  color: var(--bfc-alert-c-2);
}
.bf-datepicker-alert .bf-state-icon {
  color: var(--bfc-base-c-alert);
}

/* TIME */
.react-datepicker__time-container {
  border-left: 1px solid var(--bfc-base-c-dimmed);
}

.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box {
  width: 85px;
}

/* TIMEBOX SCROLLBAR */
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list::-webkit-scrollbar {
  height: 18px;
  width: 18px;
  background-color: transparent;
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list::-webkit-scrollbar-corner {
  background-color: transparent;
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list::-webkit-scrollbar-thumb {
  height: 30px;
  border: 5px solid transparent;
  background-color: var(--bfc-base-dimmed);
  background-clip: padding-box;
  border-radius: 10px;
  -webkit-border-radius: 10px;
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list::-webkit-scrollbar,
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list::-webkit-scrollbar-corner {
  background-color: var(--bfc-base-3);
}

/* Timelist */
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list {
  list-style: none;
  margin: 0;
  height: calc(195px + (1.7rem / 2));
  overflow-y: scroll;
  padding-right: 0px;
  padding-left: 0px;
  width: 100%;
  box-sizing: content-box;
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item {
  height: 30px;
  line-height: 30px;
  padding: 0px 4px;
  white-space: nowrap;
  font-family: 'Open Sans', sans-serif;
  font-size: var(--bf-font-size-m);
  list-style: none;
  border: 1px solid transparent;
  margin: 4px 4px 4px 8px;
  border-radius: 3px;
  text-align: center;
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-theme-1);
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item::before {
  content: none;
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item:hover {
  cursor: pointer;
  color: var(--bfc-theme-1);
  background-color: var(--bfc-base-2);
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item--selected {
  background-color: var(--bfc-theme-1);
  color: var(--bfc-base-3);
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item--disabled {
  color: var(--bfc-base-disabled);
}
.react-datepicker__time-container
  .react-datepicker__time
  .react-datepicker__time-box
  ul.react-datepicker__time-list
  li.react-datepicker__time-list-item--disabled:hover {
  cursor: default;
  background-color: transparent;
}

/* Display none to not show header for time selection */
.react-datepicker__header--time {
  display: none;
}

/* TIME INPUT */
.react-datepicker__input-time-container {
  clear: both;
  width: 100%;
  margin: 5px 0 10px 15px;
  text-align: left;
}

.react-datepicker__input-time-container .react-datepicker-time__caption {
  display: inline-block;
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
}

.react-datepicker__input-time-container
  .react-datepicker-time__input-container {
  display: inline-block;
}

.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input {
  display: inline-block;
  margin-left: 10px;
}

.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input
  input {
  width: 150px;
  background: var(--bfc-base-3);
  color: var(--bfc-base-c);
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
  border: 1px solid var(--bfc-base-c-wcag);
  padding: 4px;
  padding-left: 10px;
  outline: none;
  border-radius: 3px;
}
.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input
  input:hover {
  outline: none;
  border: 1px solid var(--bfc-base-c-inverted-2);
}
.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input
  input:focus {
  outline: none;
  border: 1px solid var(--bfc-theme-3);
}

/* Time icon */
.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input::-webkit-calendar-picker-indicator {
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 3px;
}
.bf-darkmode
  .react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input::-webkit-calendar-picker-indicator {
  background-color: var(--bfc-base-c);
}
.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input::-webkit-calendar-picker-indicator:focus {
  outline: none;
  border: 1px solid var(--bfc-theme-3);
}

.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input
  input[type='time']::-webkit-inner-spin-button,
.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input
  input[type='time']::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__input
  input[type='time'] {
  -moz-appearance: textfield;
}

.react-datepicker__input-time-container
  .react-datepicker-time__input-container
  .react-datepicker-time__delimiter {
  margin-left: 5px;
  display: inline-block;
}

/* MONTHPICKER / YEARPICKER */
.react-datepicker__year {
  margin: 8px;
}
.react-datepicker-year-header {
  padding: 12px 0;
}
.react-datepicker__month {
  display: grid;
  gap: 8px;
}
.react-datepicker__month .react-datepicker__month-wrapper,
.react-datepicker__year-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 8px;
}
.react-datepicker__month-text,
.react-datepicker__year-text {
  height: 38px;
  line-height: 38px;
  padding: 0 4px;
  text-align: center;
  border-radius: 3px;
  border: 1px dashed transparent;
  outline: none;
}
.react-datepicker__month-text:hover,
.react-datepicker__year-text:hover {
  background: var(--bfc-base-2);
  color: var(--bfc-theme-1);
}
.react-datepicker__month-text.react-datepicker__month--selected,
.react-datepicker__year-text.react-datepicker__year-text--selected {
  background: var(--bfc-theme-1);
  color: var(--bfc-base-3);
}
.react-datepicker__month-text.react-datepicker__month-text--keyboard-selected[data-focus-visible-added],
.react-datepicker__year-text.react-datepicker__year-text--keyboard-selected[data-focus-visible-added] {
  border-color: var(--bfc-theme-1);
}
.react-datepicker__month-text.react-datepicker__month--selected[data-focus-visible-added],
.react-datepicker__year-text.react-datepicker__year-text--selected[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-theme-1);
  outline-offset: 1px;
}

/* Portal (modal) view */
.react-datepicker__portal {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  left: 0;
  top: 0;
  justify-content: center;
  align-items: center;
  display: flex;
  z-index: 2147483647;
}

.react-datepicker__portal .react-datepicker__day-name,
.react-datepicker__portal .react-datepicker__day,
.react-datepicker__portal .react-datepicker__time-name {
  width: 3rem;
  line-height: 3rem;
}

@media (max-width: 400px), (max-height: 550px) {
  .react-datepicker__portal .react-datepicker__day-name,
  .react-datepicker__portal .react-datepicker__day,
  .react-datepicker__portal .react-datepicker__time-name {
    width: 2rem;
    line-height: 2rem;
  }
}

.react-datepicker__portal .react-datepicker__current-month,
.react-datepicker__portal .react-datepicker-time__header {
  font-size: 1.44rem;
}

.react-datepicker__portal .react-datepicker__navigation {
  border: 0.81rem solid transparent;
}

.react-datepicker__portal .react-datepicker__navigation--previous {
  border-right-color: #ccc;
}

.react-datepicker__portal .react-datepicker__navigation--previous:hover {
  border-right-color: #b3b3b3;
}

.react-datepicker__portal .react-datepicker__navigation--previous--disabled,
.react-datepicker__portal
  .react-datepicker__navigation--previous--disabled:hover {
  border-right-color: #e6e6e6;
  cursor: default;
}

.react-datepicker__portal .react-datepicker__navigation--next {
  border-left-color: #ccc;
}

