/* Component label */
fieldset legend.bf-label.bf-fieldgroup-legend {
  margin: 0;
  padding: 0;
}

/* Component */
.bf-fieldgroup {
  display: flex;
  position: relative;
  width: 100%;
}

/* FieldGroup.Item */
.bf-fieldgroup .bf-fieldgroup-item {
  flex-grow: 0;
}
.bf-fieldgroup .bf-fieldgroup-item > div {
  border-bottom: 2px solid var(--bfc-base-c-wcag);
  color: var(--bfc-base-c-2);
  cursor: default;
  height: var(--bfs40);
}

/* Set margin top if fieldgroup has label or description */
.bf-label-description ~ .bf-fieldgroup,
.bf-label ~ .bf-fieldgroup {
  margin-top: var(--bfs4);
}

/* Remove border radius */
.bf-fieldgroup .bf-select__control,
.bf-fieldgroup .bf-input,
.bf-fieldgroup .bf-fieldgroup-item > div {
  border-radius: 0;
}

/* All children */
.bf-fieldgroup .bf-input-container,
.bf-fieldgroup .bf-select-container,
.bf-fieldgroup .bf-datepicker-container {
  flex-grow: 1;
}

/* Vertical line splitter */
.bf-fieldgroup > * + *:before {
  content: '';
  position: absolute;
  background: var(--bfc-base-c);
  opacity: 0.2;
  height: 24px;
  width: 1px;
  bottom: 8px;
  z-index: 1;
}
.bf-fieldgroup > .bf-button:before {
  display: none;
}

/* Select support for FieldGroup */
.bf-fieldgroup .bf-select__menu-list {
  box-shadow: none;
  border: 1px solid var(--bfc-theme-3);
  border-top: none;
}

/* Checkbox support for FieldGroup */
.bf-fieldgroup .bf-checkbox-wrapper {
  background: var(--bfc-base-3);
  border-bottom: 2px solid var(--bfc-base-c-wcag);
}
.bf-fieldgroup .bf-checkbox-wrapper:hover {
  border-bottom: 2px solid var(--bfc-base-c-inverted-2);
}
.bf-fieldgroup .bf-checkbox-label,
.bf-fieldgroup .bf-checkbox-hidelabel .bf-checkbox-label {
  padding: 4px 14px;
  margin-left: 9px;
}

/* DatePicker support */
.bf-fieldgroup
  .bf-datepicker-container
  .react-datepicker__input-container
  input {
  border-radius: 0;
}

/* Set border to inset so its visible when components are next to eachother */
.bf-fieldgroup .bf-select__control--is-focused,
.bf-fieldgroup .bf-input[data-focus-visible-added],
.bf-fieldgroup .bf-datepicker[data-focus-visible-added] {
  position: relative;
  box-shadow: inset 0 0 0 1px var(--bfc-theme-3);
}

/* First child border radius */
.bf-fieldgroup .bf-input-container:first-child .bf-input,
.bf-fieldgroup .bf-select-container:first-child .bf-select__control,
.bf-fieldgroup .bf-fieldgroup-item:first-child > div,
.bf-fieldgroup > div:first-child,
.bf-fieldgroup > button:first-child,
.bf-fieldgroup
  .bf-datepicker-container:first-child
  .react-datepicker__input-container
  input {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}

/* Last child border radius */
.bf-fieldgroup .bf-input-container:last-child .bf-input,
.bf-fieldgroup .bf-select-container:last-child .bf-select__control,
.bf-fieldgroup .bf-fieldgroup-item:last-child > div,
.bf-fieldgroup > div:last-child,
.bf-fieldgroup > button:last-child,
.bf-fieldgroup
  .bf-datepicker-container:last-child
  .react-datepicker__input-container
  input {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}

/* Alert state */
.bf-fieldgroup.bf-fieldgroup-alert
  .bf-select__control:not(.bf-select__control--is-focused),
.bf-fieldgroup.bf-fieldgroup-alert
  .bf-input:not([data-focus-visible-added]),
.bf-fieldgroup.bf-fieldgroup-alert
  .react-datepicker__input-container
  input:not([data-focus-visible-added]) {
  border-bottom-color: var(--bfc-base-c-alert);
}

/* Disabled */
.bf-fieldgroup.bf-fieldgroup-disabled .bf-input-container .bf-input,
.bf-fieldgroup.bf-fieldgroup-disabled .bf-fieldgroup-item > div {
  cursor: default;
  color: var(--bfc-base-c-2);
  border-bottom-color: transparent;
  background: var(--bfc-base-dimmed);
}
