.bf-tabs-container .bf-tab-arrow-hidden {
  display: none;
}

/* #region Basic tabs */
.bf-tabs-container {
  border-bottom: 1px solid var(--bff-dimmed);
  background-color: var(--bfc-base-3);
  display: flex;
  position: relative;
}

.bf-tabs {
  display: flex;
  height: var(--bfs48);
  margin: 0;
  align-items: stretch;
  padding: 0 var(--bf-tabs-left);

  overflow-x: scroll;
  scrollbar-width: none;
  white-space: nowrap;
}

.bf-tabs::-webkit-scrollbar {
  display: none;
}

.bf-tabs > * {
  display: flex;
  align-items: stretch;
}

.bf-tabs > a,
.bf-tabs > a:focus,
.bf-tabs > a:active,
.bf-tabs > a:hover,
.bf-tabs > a:visited,
.bf-tabs > a:link {
  text-decoration: none;
}

.bf-tabs > * + * {
  margin-left: var(--bf-tabs-gap);
}

.bf-tab {
  border: 0;
  border-bottom: 2px solid transparent;
  font-size: var(--bf-font-size-m);
  font-family: inherit;
  cursor: pointer;
  color: var(--bfc-base-c-2);
  background: transparent;

  padding-top: 2px;
  display: flex;
  align-items: center;
}

.bf-tabs > a:focus,
.bf-tab:focus {
  outline: none;
}

a:hover .bf-tab,
.bf-tab:hover {
  color: var(--bfc-base-c-1);
}

.bf-tab-title {
  padding: var(--bfs2) var(--bfs4);
  border-radius: var(--bfl-border-radius);
  cursor: inherit;
  font-weight: 600;
  border: 1px dashed transparent;
}

a:not(.active)[data-focus-visible-added] :not(.bf-tab-disabled) .bf-tab-title,
.bf-tab:not(.bf-tab-active)[data-focus-visible-added] .bf-tab-title {
  border-color: var(--bfc-base-c-theme);
}

.bf-tab-title .bf-tab-icon {
  margin-left: var(--bfs4);
}

a.active .bf-tab,
.bf-tab-active {
  border-color: var(--bfc-base-c-theme);
  color: var(--bfc-base-c-1);
  pointer-events: none;
}

.bf-tab-right {
  box-shadow: -15px 0px 10px -8px var(--bfc-base-3);
  right: 0;
}

.bf-tab-left {
  box-shadow: 15px 0px 10px -8px var(--bfc-base-3);
  left: 0;
}

.bf-tab-left,
.bf-tab-right {
  position: absolute;
  padding: 0;
  height: 100%;
  border: 0;
  background: var(--bfc-base-3);
  border-radius: 0;
  display: flex;
  align-items: center;
}

.bf-tab-left.bf-button svg,
.bf-tab-right.bf-button svg {
  margin: 0 var(--bfs8);
  border-radius: 50%;
  width: 21px;
  height: 21px;
  padding: 3px;
}
.bf-tab-left.bf-button:hover svg,
.bf-tab-right.bf-button:hover svg {
  background-color: var(--bfc-base-dimmed);
}

.bf-tab-left:hover,
.bf-tab-right:hover {
  background: var(--bfc-base-3);
}

.bf-tab-content {
  height: auto;
  background-color: var(--bfc-base);
}

.bf-tab-disabled {
  cursor: default;
  pointer-events: none;
  color: var(--bfc-base-c-disabled);
}

@media (max-width: 599.9px) {
  .bf-tabs {
    --bf-tabs-left: var(--bfs8);
    --bf-tabs-gap: var(--bfs8);
  }
}
@media (min-width: 600px) and (max-width: 1919.9px) {
  .bf-tabs {
    --bf-tabs-left: 20px;
    --bf-tabs-gap: var(--bfs16);
  }
}
@media (min-width: 1920px) {
  .bf-tabs {
    --bf-tabs-left: 36px;
    --bf-tabs-gap: var(--bfs32);
  }
}
/* #endregion*/

/* #region Styled tabs */
.bf-tabs-styled {
  background-color: var(--bfc-base-2);
  height: var(--bfs56);
  border-bottom: 1px solid var(--bff-dimmed);
  align-items: flex-end;
}

.bf-tabs-styled .bf-tabs {
  margin-bottom: -1px;
  height: 100%;
  display: flex;
  align-items: flex-end;
}

a:hover .bf-tabs-styled .bf-tab,
.bf-tabs-styled .bf-tab:hover {
  color: var(--bfc-base-c-theme);
}

.bf-tabs-styled .bf-tab-right,
.bf-tabs-styled .bf-tab-left {
  box-shadow: none;
  background-color: var(--bfc-base-2);
  border-bottom: 1px solid var(--bff-dimmed);
  transform: translateY(1px);
  padding-top: var(--bfs8);
}

.bf-tabs-styled .bf-tab {
  padding: var(--bfs12) var(--bf-tabs-padding);
  border-bottom: none;
  border-left: 1px solid transparent;
  border-right: 1px solid transparent;
  border-top: 1px solid transparent;
  height: var(--bfs48);
}

.bf-tabs-styled .bf-tabs > * + * {
  margin-left: 0;
}

.bf-tabs-styled a.active .bf-tab,
.bf-tabs-styled .bf-tab-active {
  border-color: var(--bff-dimmed);
  background-color: var(--bfc-base-1);
  color: var(--bfc-base-c-1);
}
.bf-tabs-styled .bf-tab-title {
  padding: 0;
}

@media (max-width: 599.9px) {
  .bf-tabs-styled .bf-tabs {
    --bf-tabs-left: var(--bfs4);
    --bf-tabs-padding: 6px; /* 8px - two 1px borders */
  }
}
@media (min-width: 600px) and (max-width: 1919.9px) {
  .bf-tabs-styled .bf-tabs {
    --bf-tabs-left: var(--bfs12);
    --bf-tabs-padding: 10px; /* 12px - two 1px borders */
  }
}
@media (min-width: 1920px) {
  .bf-tabs-styled .bf-tabs {
    --bf-tabs-left: 20px;
    --bf-tabs-padding: 18px; /* 20px - two 1px borders */
  }
}
/* #endregion */
