.bf-spinner-container {
  width: auto;
  height: auto;
  display: inline-flex;
  flex-direction: column;
}

.bf-overlay .bf-spinner-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bf-spinner-container .bf-spinner-color {
  color: var(--bfc-theme);
}

.bf-lightmode .bf-overlay .bf-spinner-color {
  color: var(--bfc-theme-c);
}

.bf-spinner-container .bf-icon-spinner {
  animation: bf-spin 2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  margin: auto;
}

/* SPINNER OVERLAY */
.bf-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--bfc-overlay);
  z-index: 9999;
}

/* SPINNER LABEL */
.bf-spinner-label {
  width: inherit;
  text-align: center;
  margin-bottom: 0;
}
.bf-overlay .bf-spinner-label {
  color: hsl(0, 0%, 90%);
}

/* ANIMATION */
@keyframes bf-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(720deg);
  }
}
