/* basic button */
.bf-button {
  border-radius: var(--bfl-border-radius);
  background: var(--bfc-base-3);
  color: var(--bfc-theme);
  height: var(--bfs40);
  padding: 0 var(--bfs16);
  min-width: 7.5rem;
  max-width: 15.5rem;
  border: 1px solid var(--bfc-base-c-wcag);
  font-size: var(--bf-font-size-m);
  font-family: inherit;
  cursor: pointer;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  transition: background-color var(--bf-animation-duration) ease-in-out;
}

.bf-button:hover,
a.bf-button:hover {
  background-color: var(--bfc-base-2);
}
.bf-button:focus {
  outline: none;
}
.bf-button[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}
/* remove firefox dotted line on focus*/
.bf-button::-moz-focus-inner {
  border: 0;
}
.bf-button:active,
a.bf-button:active {
  background-color: var(--bfc-base-1);
}

/* button-styled <a> elements */
a.bf-button,
.bf-content a.bf-button {
  display: inline-flex;
  text-decoration: none;
  align-items: center;
  justify-content: center;
  max-width: none;
  text-decoration: none;
  box-shadow: none;
  border: 1px solid var(--bfc-base-c-wcag);
}
a.bf-button:focus,
.bf-content a.bf-button:focus {
  outline: none;
}
a.bf-button[data-focus-visible-added],
.bf-content a.bf-button[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}

/* #region Filled */
.bf-button-filled,
a.bf-button-filled:link,
a.bf-button-filled:visited {
  background: var(--bfc-theme);
  color: var(--bfc-theme-c-1);
  border: 1px solid transparent;
}
.bf-button-filled:hover,
a.bf-button-filled:hover {
  background-color: var(--bfc-theme-2);
}
.bf-button-filled[data-focus-visible-added],
a.bf-button-filled[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}
.bf-button-filled:active,
a.bf-button-filled:active {
  background-color: var(--bfc-theme-3);
}
/* #endregion */

/* #region Inactive + Filled */
.bf-button-filled.bf-button-inactive {
  background: var(--bfc-base-c-inverted-2);
  color: var(--bfc-base-1);
  border: 1px solid transparent;
}
.bf-button-filled.bf-button-inactive:hover {
  background-color: var(--bfc-base-c-inverted-1);
  color: var(--bfc-base-1);
  border-color: transparent;
}

.bf-button-filled.bf-button-inactive[data-focus-visible-added] {
  background-color: var(--bfc-base-c-inverted-2);
  color: var(--bfc-base-1);
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}
.bf-button-filled.bf-button-inactive:active {
  background: var(--bfc-base-c-inverted-2);
  color: var(--bfc-base);
  border-color: transparent;
}
/* #endregion */

/* #region Inactive + Outline */
.bf-button-inactive,
.bf-button-outline.bf-button-inactive {
  background: var(--bfc-base-dimmed);
  color: var(--bfc-base-c-inverted-2);
  border: 1px solid var(--bfc-base-c-inverted-2);
}
.bf-button-inactive:hover,
.bf-button-inactive[data-focus-visible-added],
.bf-button-outline.bf-button-inactive:hover,
.bf-button-outline.bf-button-inactive[data-focus-visible-added] {
  background: var(--bfc-base-dimmed);
  color: var(--bfc-base-c-inverted-1);
  border: 1px solid var(--bfc-base-c-inverted-1);
}
.bf-button-inactive:active,
.bf-button-outline.bf-button-inactive:active {
  background: var(--bfc-base-dimmed);
  color: var(--bfc-base-c-inverted-2);
  border: 1px solid var(--bfc-base-c-inverted-2);
}
/* #endregion */

/* #region Alert + Outline */
.bf-button-outline.bf-button-alert {
  background: var(--bfc-base-3);
  color: var(--bfc-base-c-alert);
  border: 1px solid var(--bfc-base-c-alert);
}
.bf-button-outline.bf-button-alert:hover {
  background: var(--bfc-alert-fade-2);
  border: 1px solid var(--bfc-alert-fade-2-c);
}
.bf-button-outline.bf-button-alert[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}
.bf-button-outline.bf-button-alert:active {
  background: var(--bfc-alert-fade);
  border: 1px solid var(--bfc-alert-fade-c);
  color: var(--bfc-alert-fade-c);
}
/* #endregion */

/* #region Alert + Filled - NOT UX APPROVED */
.bf-button-filled.bf-button-alert {
  background: var(--bfc-alert-fade-2-c);
  color: var(--bfc-alert-fade-2);
}
.bf-button-filled.bf-button-alert:hover {
  background: var(--bfc-alert-fade-c);
  color: var(--bfc-alert-fade);
}
/* #endregion */

/* #region Outline */
.bf-button-outline,
a.bf-button-outline {
  border-color: var(--bff-theme);
}
.bf-button-outline:hover,
a.bf-button-outline:hover {
  border-color: var(--bff-theme);
  background: var(--bfc-theme-c);
}
.bf-button-outline[data-focus-visible-added],
a.bf-button-outline[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}
.bf-button-outline:active,
a.bf-button-outline:active {
  border-color: var(--bfc-base-c-theme);
  background: var(--bfc-theme-c-2);
}
/* #endregion */

/* #region Disabled */
.bf-button:disabled,
.bf-button:disabled:hover {
  border-color: var(--bff-disabled);
  color: var(--bfc-base-c-2);
  background: var(--bfc-base);
  cursor: default;
}
.bf-button-filled:disabled,
.bf-button-filled:disabled:hover {
  background: var(--bfc-base-disabled);
  color: var(--bfc-base-c-2);
}
/* #endregion */

.bf-button-stretch {
  width: auto;
  min-width: 0;
  max-width: none;
}

.bf-button-left-icon .svg-inline--fa,
.bf-button-left-icon .fa {
  margin-right: var(--bfs4);
}
.bf-button-right-icon .svg-inline--fa,
.bf-button-right-icon .fa {
  margin-left: var(--bfs4);
}

.bf-button-small {
  padding: 0.34375rem 0.5rem;
  min-width: 0;
  height: var(--bfs32);
}

/* #region Button.Group */
.bf-button-group {
  padding: 3px;
  border: 1px solid var(--bfc-base-c-wcag);
  border-radius: var(--bfl-border-radius);
  background: var(--bfc-base-3);
  display: inline-flex;
  flex-wrap: nowrap;
}

.bf-button-group .bf-button {
  margin-right: var(--bfs4);
  border: 0;
  border-radius: 3px;
  height: 32px;
  padding: 5.5px var(--bfs8);
  min-width: 0;
  color: var(--bfc-base-c-1);
  border: 1px dashed transparent;
  transition: none;
}

.bf-button-group .bf-button:last-child {
  margin-right: 0;
}

.bf-button-group .bf-button:hover {
  background-color: transparent;
  color: var(--bfc-base-c-theme);
}

.bf-button-group .bf-button-selected,
.bf-button-group .bf-button-selected:hover,
.bf-button-group .bf-button-selected[data-focus-visible-added] {
  background-color: var(--bfc-theme-1);
  color: var(--bfc-theme-c-1);
}

.bf-button-group .bf-button-inactive,
.bf-button-group .bf-button-inactive:hover,
.bf-button-group .bf-button-inactive[data-focus-visible-added],
.bf-button-group .bf-button:disabled,
.bf-button-group .bf-button:disabled:hover {
  background-color: var(--bfc-base-3);
  color: var(--bfc-base-c-disabled);
  border: 0;
  cursor: default;
}
.bf-button-group .bf-button[data-focus-visible-added] {
  outline: none;
  border-color: var(--bff-theme);
  background-color: transparent;
}
.bf-button-group .bf-button-selected[data-focus-visible-added] {
  box-shadow: inset 0 0 0 1px var(--bfc-base-3),
    inset 0 0 0 1000px var(--bfc-theme);
  border-radius: 6px;
}
/* #endregion */
