/* Nav.Item */
.bf-nav-item {
  position: relative;
  display: block;
  color: inherit;
  text-decoration: none;
  z-index: 1;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.bf-nav-group-item-circle-wrapper,
.bf-nav-group-item-dot-circle {
  display: none;
}

.bf-nav-group-item-circle {
  color: var(--bff-dimmed);
}

/* sidebar Nav.Item */
.bf-nav-side .bf-nav-item {
  padding: 0 0 0 var(--bfs24);
  line-height: var(--bf-nav-side-item-height);
  overflow: hidden;
  border: 1px solid transparent;
}
.bf-nav-side .bf-nav-item-icon {
  display: inline-block;
  text-align: center;
  width: var(--bfs56);
  color: var(--bfc-base-c-wcag);
}

/* top bar Nav.Item */
.bf-nav-top .bf-nav-item {
  border: 1px solid transparent;
  border-bottom: var(--bfl-border);
  border-right: var(--bfl-border);
  display: flex;
  align-items: center;
  font-size: var(--bf-font-size-m);
  padding: var(--bfs8) var(--bfs16);
  justify-content: center;
  min-width: 56px;
  height: 100%;
}
.bf-nav-top-content .bf-nav-item {
  border-right-color: transparent;
  border-left: var(--bfl-border);
}

.bf-nav-top .bf-nav-item-icon {
  font-size: var(--bf-font-size-l);
  margin-right: var(--bfs8);
}
.bf-nav-top .bf-nav-item-icon-only .bf-nav-item-icon {
  margin-right: 0;
}

/* mobile Nav.Item */
.bf-nav-mobile .bf-nav-item {
  padding: 14px var(--bfs16);
  border-radius: var(--bfl-border-radius);
  border: 1px solid var(--bfc-base-dimmed);
  margin: var(--bfs8) var(--bfs12) 0 var(--bfs12);
  background-color: var(--bfc-base-3);
}
.bf-nav-mobile .bf-nav-item-icon {
  display: inline-block;
  text-align: center;
  width: var(--bfs56);
  color: var(--bfc-base-c-wcag);
}

/* link and button states */
a:hover > .bf-nav-item,
button:hover > .bf-nav-item {
  color: var(--bft-theme);
}
.bf-nav a[data-focus-visible-added] > .bf-nav-item,
.bf-nav button[data-focus-visible-added] > .bf-nav-item {
  border: 1px dashed var(--bff-theme);
}
a:hover > .bf-nav-item .bf-nav-item-icon,
a:hover > .bf-nav-item .bf-nav-group-item-circle,
a.active .bf-nav-item-icon,
button:hover > .bf-nav-item .bf-nav-item-icon,
button.active .bf-nav-item-icon {
  color: inherit;
}

/* selected */
a.active > .bf-nav-item,
button.active > .bf-nav-item {
  font-weight: 600;
  color: var(--bft-theme);
}

/* selected + hover */
a.active:hover > .bf-nav-item,
button.active:hover > .bf-nav-item {
  color: var(--bfc-theme-3);
}

.bf-nav-mobile .bf-nav-item-with-icon,
.bf-nav-side .bf-nav-item-with-icon {
  padding-left: 0;
}
