  --bfc-base-c-alert: hsl(var(--bfc-alert-hs), 40%); /* not for text */
  --bfc-base-c-wcag: hsl(var(--bfc-base-hs), 50%); /* not for text */
  --bfc-base-c-dimmed: hsl(var(--bfc-base-hs), 85%); /* not for text */
  --bfc-base-c-disabled: hsl(var(--bfc-base-hs), 65%);

  --bfc-base-dimmed-hsl: var(--bfc-base-hs), 85%;
  --bfc-base-dimmed: hsl(var(--bfc-base-dimmed-hsl));
  --bfc-base-dimmed-c: var(--bfc-base-c-1);

  --bfc-base-disabled: hsl(var(--bfc-base-hs), 65%);
