  --bfc-theme-c-1: hsl(var(--bfc-theme-c-hsl));
  --bfc-theme-c-2-hsl: var(--bfc-theme-hs), 90%;
  --bfc-theme-c-2: hsl(var(--bfc-theme-c-2-hsl));
  --bfc-theme-c: var(--bfc-theme-c-1);
  --bfc-theme-gradient: linear-gradient(
    90deg,
    var(--bfc-theme-3),
    var(--bfc-theme-1)
  );

  /* Green (light mode) */
  --bfc-success-hs: 120, 100%;
  --bfc-success-hsl: var(--bfc-success-hs), 35%;
  --bfc-success: hsl(var(--bfc-success-hsl));
  --bfc-success-c-hsl: var(--bfc-success-hs), 5%;
  --bfc-success-c: hsl(var(--bfc-success-c-hsl));

  --bfc-success-fade-hsl: var(--bfc-success-hs), 90%;
