  --bfc-theme-fade: hsl(var(--bfc-theme-fade-hsl));
  --bfc-theme-fade-c-hsl: var(--bfc-theme-hsl);
  --bfc-theme-fade-c: hsl(var(--bfc-theme-fade-c-hsl));

