.bf-pagination {
  white-space: nowrap;
  text-align: center;
  font-size: var(--bf-font-size-m);
  user-select: none;
}
.bf-pagination-button {
  padding: 0 4px;
  display: inline-block;
  font-family: inherit;
  font-size: inherit;
  line-height: 1;
  background-color: transparent;
  color: var(--bfc-base-c-1);
  border: none;
  outline: none;
  min-width: 28px;
  height: 28px;
  border-radius: 20px;
  margin: 0 var(--bfs8);
  text-align: center;
  cursor: pointer;
  border: 1px dashed transparent;
}
.bf-pagination-prev,
.bf-pagination-next {
  background-color: transparent;
  border: none;
  font-family: inherit;
  font-size: inherit;
  line-height: 1;
  padding: 0;
  margin: 0 var(--bfs8);
  outline-offset: 4px;
}
.bf-pagination-prev svg {
  margin-right: var(--bfs4);
}
.bf-pagination-next svg {
  margin-left: var(--bfs4);
}
.bf-pagination-dots {
  display: inline-block;
  text-align: center;
}
.bf-pagination > .to-small {
  padding: 0 var(--bfs8);
}

.bf-pagination-button-arrow {
  color: var(--bfc-base-c-theme);
  margin: 0;
}

.bf-pagination-button[data-focus-visible-added] {
  border-color: var(--bff-theme);
}
.bf-pagination-button:hover {
  background-color: var(--bfc-base-3);
  color: var(--bfc-theme-1);
}

.bf-pagination-button:disabled {
  cursor: default;
}

.bf-pagination-button-arrow:disabled,
.bf-pagination-button-arrow:disabled[data-focus-visible-added],
.bf-pagination-button-arrow:disabled:hover,
.bf-pagination-button-arrow:disabled:active {
  color: var(--bfc-base-disabled);
  background-color: transparent;
}

.bf-pagination-button-selected,
.bf-pagination-button-selected[data-focus-visible-added],
.bf-pagination-button-selected:hover,
.bf-pagination-button-selected:active {
  background-color: var(--bfc-theme-1);
  color: var(--bfc-theme-c-1);
}
