:root {
  --bf-card-padding: var(--bfs12);
}
.bf-card {
  background-color: var(--bfc-base-3);
  font-size: var(--bf-font-size-m);
  position: relative;
  min-height: 0;
  display: flow-root;
  border-radius: var(--bfl-border-radius);
}
.bf-card > a,
.bf-card > a:link,
.bf-card > a:visited,
.bf-card > a:focus,
.bf-card > a:hover,
.bf-card > a:active {
  padding: 0;
  background: none;
  text-decoration: none;
  color: inherit;
  display: block;
}
.bf-card > button {
  padding: 0;
  outline: none;
  display: block;
  width: 100%;
  text-align: inherit;
  background: inherit;
  border: inherit;
  font: inherit;
  cursor: pointer;
}
.bf-card > button[data-focus-visible-added],
.bf-card > a[data-focus-visible-added] {
  outline: 1px dashed var(--bff-theme);
}

/* when a link (or button) wraps the whole card content,
stretch it to full height to make sure whole area is clickable */
.bf-card > a:only-child,
.bf-card > button:only-child {
  height: 100%;
}

.bf-card-align-center {
  text-align: center;
}

.bf-card-image {
  min-height: 150px;
  background-size: cover;
  background-position: 50% 50%;
}
.bf-card-image:first-child {
  border-top-left-radius: var(--bfl-border-radius);
  border-top-right-radius: var(--bfl-border-radius);
}

.bf-card-logo {
  height: 70px;
  width: 70px;
  border-radius: 70px;
  margin: var(--bf-card-padding) auto;
  font-size: var(--bf-font-size-h2);
  background-color: var(--bfc-base);
  background-size: cover;
  background-position: 50% 20%;
  position: relative;
}
.bf-card-image + .bf-card-logo {
  margin-top: -35px;
}
.bf-card-logo-icon {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bf-card-title {
  margin: var(--bf-card-padding);
  margin-bottom: var(--bfs4);
  color: var(--bfc-base-c);
  font-size: var(--bf-font-size-l);
}
.bf-card-title:last-child {
  margin-bottom: var(--bf-card-padding);
}
a .bf-card-title,
button .bf-card-title {
  color: var(--bff-theme);
  text-underline-offset: var(--bf-underline-offset);
}
a.bf-neutral-link .bf-card-title,
button.bf-neutral-link .bf-card-title {
  color: inherit;
}
a:hover .bf-card-title,
button:hover .bf-card-title {
  text-decoration: underline;
}
.bf-card > .bf-card-title:last-child {
  margin-bottom: var(--bf-card-padding);
}

.bf-card-content {
  margin: var(--bf-card-padding) 0 var(--bf-card-padding) 0;
  padding: 0 var(--bf-card-padding);
  color: var(--bfc-base-c-2);
  text-decoration: none;
}
.bf-card-content + .bf-card-content {
  border-top: 1px solid var(--bfc-base-c-dimmed);
  padding-top: var(--bf-card-padding);
}
.bf-card-title + .bf-card-content {
  margin-top: var(--bfs4);
}
.bf-card-content > :first-child {
  margin-top: 0;
}

button.bf-card-button {
  padding: var(--bfs12) var(--bf-card-padding);
  border-top: 1px solid var(--bfc-base-c-dimmed);
  font-weight: 600;
  text-align: center;
  margin-top: var(--bf-card-padding);
}

/* calculate card image height based on ratio, screen width, column size,
 while accounting for paddings, gaps and sidebar width */
:root {
  /* default to 16:9 ratio for card images */
  --bf-card-image-ratio: calc(16 / 9);
}
.bf-card-image {
  /* helper vars to keep aspect ratio on card images for all screen sizes
  attempt to calculate precise height
  start with columnWidth - 100% viewport width divided by column count.
  account for bfl-grid with padding around the page
  paddings and gaps added up together doesn't apply to *each* column,
  so divide by column count.
  finally, divide by the ratio to get final image height

  imageHeight = (columnWidth - paddingsAndGaps / columCount) / ratio
  */
  --bf-card-image-height: calc(
    (
        (100vw / var(--bfl-columns, 1)) -
          (
            var(--bfl-page-padding, 0px) * 2 + var(--bfl-gap, 0px) *
              (var(--bfl-columns, 1) - 1)
          ) / var(--bfl-columns, 1)
      ) / var(--bf-card-image-ratio, 1.7777)
  );
}

@media (min-width: 1600px) {
  .bf-card-image {
    /* same as before but also account for sidebar width
    imageHeight = (columnWidth - (paddingsAndGaps + sidebarWidth) / columnCount) / ratio
    */
    --bf-card-image-height: calc(
      (
          (100vw / var(--bfl-columns, 1)) -
            (
              var(--bfl-page-padding, 0px) * 2 + var(--bfl-gap, 0px) *
                (var(--bfl-columns, 1) - 1) + var(--bf-nav-side-width, 0px)
            ) / var(--bfl-columns, 1)
        ) / var(--bf-card-image-ratio, 1.7777)
    );

    /* transition height in sync with sidebar collapse / expand for desktop */
    transition: height var(--bf-nav-side-transition);
  }
}

/* responsive padding */
.bf-card-no-padding {
  --bf-card-padding: 0;
}
.bf-card-padding-m {
  --bf-card-padding: var(--bfs12);
}
.bf-card-padding-l {
  --bf-card-padding: var(--bfs16);
}
@media (min-width: 1280px) {
  .bf-card-padding-m {
    --bf-card-padding: var(--bfs16);
  }
  .bf-card-padding-l {
    --bf-card-padding: var(--bfs24);
  }
}
