.bf-checkboxcard-wrapper {
  font-size: var(--bf-font-size-m);
  display: contents; /* to make sure each checkboxcard are the same size when on the same row */ 
}

.bf-checkboxcard-wrapper .bf-checkboxcard-label {
  background-color: var(--bfc-base-3);
  border: 1px solid var(--bfc-base-dimmed);
  border-radius: var(--bfl-border-radius);
  cursor: pointer;
  display: block;
  overflow: hidden;
}

.bf-checkboxcard-input {
    /* you cannot focus on a field with
   * visibility: hidden; or display: none;
	 * so we hide it some other way */
   position: absolute;
   width: 0;
   height: 0;
   opacity: 0;
}

.bf-checkboxcard-header {
  background-color: var(--bfc-base-2);
}

.bf-checkboxcard-content {
  padding: var(--bfs12);
  font-weight: 400;
  color: var(--bfc-base-c-2);
}

.bf-checkboxcard-label-text {
  display: block;
  position: relative;
  margin-bottom: var(--bfs8);
  padding-right: var(--bfs24);
  color: var(--bfc-base-c-1);
  font-weight: 600;
}

.bf-checkboxcard-label-text > svg {
  margin-right: var(--bfs4);
}

/* hover state */ 
.bf-checkboxcard-label:hover .bf-checkboxcard-label-text,
.bf-checkboxcard-label:hover .bf-checkboxcard-icon {
  color: var(--bfc-theme-2)
}

/* focus state */ 
.bf-checkboxcard-input[data-focus-visible-added] ~ .bf-checkboxcard-label {
  outline: 1px dashed var(--bfc-theme-1);
  outline-offset: 1px;
}

/* disabled */
.bf-checkboxcard-input:disabled ~ .bf-checkboxcard-label .bf-checkboxcard-content,
.bf-checkboxcard-input:disabled ~ .bf-checkboxcard-label .bf-checkboxcard-label-text,
.bf-checkboxcard-input:disabled ~ .bf-checkboxcard-label .bf-checkboxcard-icon {
  color: var(--bfc-base-c-disabled);
  border-color: var(--bfc-base-disabled);
  cursor: default;
}

.bf-checkboxcard-input:disabled ~ .bf-checkboxcard-label .bf-checkboxcard-header {
  cursor: default;
}

.bf-checkboxcard-icon {
  position: absolute;
  right: 0;
  top: 0;
  color: var(--bfc-theme-1);
}