.bf-sidebar {
  --bf-sidebar-item-height: var(--bfs56);
  --bf-sidebar-border-right: var(--bfl-border);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: var(--bf-sidebar-width);
  background-color: var(--bfc-base-3);
  color: var(--bfc-base-c);
  font-size: var(--bf-font-size-m);
  border-right: var(--bf-sidebar-border-right);
  transition: width var(--bf-sidebar-transition);
}

.bf-sidebar-standalone {
  --bf-sidebar-border-right: none;
  box-shadow: 4px 0 10px 0 rgba(0, 0, 0, 0.15);
}

.bf-sidebar-appname {
  padding: 0 21px;
  line-height: var(--bfs32);
  font-size: var(--bf-font-size-s);
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-right: var(--bf-sidebar-border-right);
  border-bottom: var(--bfl-border);
  color: var(--bfc-base-c-2);
  background-color: var(--bfc-base-2);
}

.bf-sidebar-content {
  position: fixed;
  top: var(--bf-topbar-height);
  bottom: 0;
  left: 0;
  width: var(--bf-sidebar-width);
  border-right: var(--bf-sidebar-border-right);
  overflow-y: auto;
  transition: width var(--bf-sidebar-transition);
  /* firefox scrollbar */
  /* scrollbar-color: hsla(var(--bfc-base-c-inverted-hsl), 0.2) transparent;
  scrollbar-width: thin; */
}
/* chrome scrollbar */
/* .bf-sidebar-content::-webkit-scrollbar {
  width: 10px;
  border: none;
  background-color: transparent;
}
.bf-sidebar-content::-webkit-scrollbar-thumb {
  background-color: var(--bfc-base-dimmed);
  border-radius: 10px;
} */

/* make space to the left of content for the fixed Sidebar */
.bf-sidebar ~ main,
.bf-sidebar ~ .bf-main {
  margin-left: var(--bf-sidebar-width);
  transition: margin var(--bf-sidebar-transition);
  position: relative;
  z-index: 1;
}
.bf-sidebar-collapsed ~ main,
.bf-sidebar-collapsed ~ .bf-main {
  margin-left: var(--bf-sidebar-width-collapsed);
}
.bf-small-it-logo {
  display: none;
}
.bf-sidebar-collapsed.bf-sidebar .bf-small-it-logo {
  display: inline;
}
.bf-sidebar-collapsed.bf-sidebar .bf-full-intility-logo {
  display: none;
}

/* make space to the left of topbar for the fixed Sidebar */
.bf-sidebar ~ .bf-topbar {
  margin-left: var(--bf-sidebar-width);
  transition: margin var(--bf-sidebar-transition);
}
.bf-sidebar-collapsed ~ .bf-topbar {
  margin-left: var(--bf-sidebar-width-collapsed);
}

.bf-sidebar-content > hr,
.bf-sidebar-group-items > hr {
  padding: 0;
  margin: 0;
  border: var(--bfl-border);
  border-bottom: none;
}
.bf-sidebar-group-items > hr {
  margin: var(--bfs12) 0;
}

.bf-sidebar-item {
  position: relative;
  display: block;
  padding: 0 0 0 var(--bfs24);
  line-height: var(--bf-sidebar-item-height);
  color: inherit;
  text-decoration: none;
  z-index: 1;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  outline: none;
  border: 1px solid transparent;
}
/* remove firefox outline */
.bf-sidebar-item::-moz-focus-inner {
  border: 0;
}

.bf-sidebar-item .bf-sidebar-item-icon {
  display: inline-block;
  text-align: center;
  width: var(--bfs56);
  color: var(--bfc-base-c-wcag);
}

a:hover > .bf-sidebar-item {
  color: var(--bft-theme);
}
a[data-focus-visible-added] > .bf-sidebar-item {
  border: 1px dashed var(--bff-theme);
}
a:hover > .bf-sidebar-item .bf-sidebar-item-icon,
a[data-focus-visible-added] > .bf-sidebar-item .bf-sidebar-item-icon,
a.active .bf-sidebar-item-icon {
  color: inherit;
}
a.active > .bf-sidebar-item,
a.active:hover > .bf-sidebar-item {
  color: var(--bft-theme);
}

.bf-sidebar-item-with-icon {
  padding-left: 0;
}

.bf-sidebar-standalone .bf-sidebar-darkmode-toggle {
  display: block;
  border-top: var(--bfl-border);
}
.bf-sidebar-collapsible .bf-sidebar-darkmode-toggle {
  bottom: calc(var(--bf-sidebar-item-height) + 2px);
}

/* collapsible sidebar styles */
.bf-sidebar-collapsible .bf-sidebar-content,
.bf-sidebar-standalone .bf-sidebar-content {
  bottom: var(--bf-sidebar-item-height);
}
.bf-sidebar-collapsible.bf-sidebar-standalone .bf-sidebar-content {
  /* standalone menu also displays dark mode toggle button
  also account for top and bottom border, four total with two fixed bottom items */
  bottom: calc(var(--bf-sidebar-item-height) * 2 + 4px);
}

.bf-sidebar-collapse-button,
.bf-sidebar-darkmode-toggle {
  display: none;
  position: fixed;
  bottom: 0;
  width: var(--bf-sidebar-width);
  border-right: var(--bf-sidebar-border-right);
  background-color: var(--bfc-base-3);
  transition: width var(--bf-sidebar-transition);
}
.bf-sidebar-collapse-button {
  background-color: var(--bfc-base-2);
  color: var(--bfc-base-c-theme);
}

.bf-sidebar-collapse-button .bf-sidebar-item-icon {
  color: var(--bfc-base-c-theme);
}
.bf-sidebar-collapsible .bf-sidebar-collapse-button {
  display: block;
}
.bf-sidebar-collapsed,
.bf-sidebar-collapsed .bf-sidebar-content,
.bf-sidebar-collapsed .bf-sidebar-collapse-button,
.bf-sidebar-collapsed .bf-sidebar-darkmode-toggle {
  width: var(--bf-sidebar-width-collapsed);
}
.bf-sidebar-collapsed .bf-sidebar-appname {
  padding-left: 0;
  padding-right: 0;
  text-align: center;
}

/* used internally for group collapse and group item circle */
.bf-sidebar-item-righticon {
  position: absolute;
  right: var(--bfs16);
}

/** sidebar groups / multiple level menu **/

/* hide group expanded indicator (righticon) when sidebar is collapsed */
.bf-sidebar-collapsed .bf-sidebar-item-righticon {
  display: none;
}
.bf-sidebar-group-item-icon-circle,
.bf-sidebar-group-item-icon-dot-circle {
  color: var(--bff-dimmed);
}
a:hover > .bf-sidebar-item .bf-sidebar-group-item-icon-circle,
a:hover > .bf-sidebar-item .bf-sidebar-group-item-icon-dot-circle,
a[data-focus-visible-added]
  > .bf-sidebar-item
  .bf-sidebar-group-item-icon-circle,
a[data-focus-visible-added]
  > .bf-sidebar-item
  .bf-sidebar-group-item-icon-dot-circle {
  color: inherit;
}

.bf-sidebar-group-item-icon,
.bf-sidebar-group-item-icon-dot-circle {
  /* circles are hidden for sidebar items outside of a group */
  display: none;
}
.bf-sidebar-group-items {
  background-color: var(--bfc-base-2);
  padding: var(--bfs12) 0;
}
.bf-sidebar-group-popup .bf-sidebar-group-item-icon,
.bf-sidebar-group-items .bf-sidebar-group-item-icon {
  display: inline;
}
/* make space for circle icon or rightIcon */
.bf-sidebar-group-popup .bf-sidebar-item,
.bf-sidebar-group-items .bf-sidebar-item,
.bf-sidebar-item.bf-sidebar-item-with-right-icon {
  padding-right: var(--bfs32);
}
/* show different icon based on presence of active class */
.bf-sidebar-group-popup a.active .bf-sidebar-group-item-icon-circle,
.bf-sidebar-group-items a.active .bf-sidebar-group-item-icon-circle {
  display: none;
  color: inherit;
}
.bf-sidebar-group-popup a.active .bf-sidebar-group-item-icon-dot-circle,
.bf-sidebar-group-items a.active .bf-sidebar-group-item-icon-dot-circle {
  display: inline;
  color: inherit;
}

.bf-sidebar-group-items .bf-sidebar-item,
.bf-sidebar-group-items a > .bf-sidebar-item {
  background-color: var(--bfc-base-2);
  font-size: var(--bf-font-size-s);
  line-height: var(--bfs40);
}
.bf-sidebar-group-expanded-icon {
  float: right;
}

/* sidebar popup when collapsed */
.bf-sidebar-group-active:not([data-focus-visible-added]):not(:hover) {
  background-color: var(--bfc-base-2);
  color: var(--bfc-base-c-theme);
}
.bf-sidebar-group-active:not([data-focus-visible-added]):not(:hover)
  .bf-sidebar-item-icon {
  color: var(--bfc-base-c-theme);
}
.bf-sidebar-group-popup {
  min-width: 230px;
  color: var(--bfc-base-c);
  background-color: var(--bfc-base-3);
  border-radius: var(--bfl-border-radius);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.15);
  padding-bottom: var(--bfs12);
}

.bf-sidebar-group-popup .tippy-content {
  padding: 0;
}

.bf-sidebar-group-popup[data-placement^='right'] > .tippy-arrow::before {
  border-right-color: var(--bfc-base-3);
}

.bf-sidebar-group-popup .bf-sidebar-item {
  justify-content: flex-start;
  font-size: var(--bf-font-size-s);
  box-shadow: none;
  padding-left: var(--bfs24);
  line-height: var(--bfs40);
  cursor: pointer;
}

.bf-sidebar-group-popup a:hover,
.bf-sidebar-group-popup a[data-focus-visible-added],
.bf-sidebar-group-popup a:hover > .bf-sidebar-item,
.bf-sidebar-group-popup a[data-focus-visible-added] > .bf-sidebar-item {
  background-color: initial;
}

.bf-sidebar-group-popup-title {
  border-bottom: 1px solid var(--bff-dimmed);
  color: var(--bff-wcag);
  padding-left: 24px;
  line-height: var(--bf-sidebar-item-height);
  font-size: var(--bf-font-size-m);
  font-style: italic;
  margin-bottom: var(--bfs12);
}

.bf-sidebar-group-popup hr {
  padding: 0;
  margin: var(--bfs12) 0;
  border: var(--bfl-border);
  border-bottom: none;
  margin-top: var(--bfs8);
}

.bf-sidebar a,
.bf-sidebar a:link,
.bf-sidebar a:visited,
.bf-sidebar a:hover,
.bf-sidebar a:active,
.bf-sidebar a:focus {
  text-decoration: none;
  display: block;
  box-shadow: none;
  margin: 0;
  padding: 0;
  color: inherit;
  outline: none;
}

