.bf-banner {
  background-color: var(--bfc-base-c-theme);
  color: var(--bfc-theme-c-1);
  border-radius: 0 0 3px 3px;
  outline: none;
  padding: var(--bfs8) 10px;

  position: absolute;
  right: var(--bfs12);
  top: -8px;
  z-index: 2;
}

.bf-banner-left {
  left: var(--bfs12);
  right: auto;
}

.bf-banner::before {
  content: '';
  position: absolute;
  top: 0;
  left: -5px;
  display: block;
  height: 0;
  width: 0;
  border-bottom: 8px solid var(--bfc-theme-3);
  border-left: 5px solid transparent;
}

button.bf-banner {
  cursor: pointer;
  border: none;
  line-height: inherit;
  font-size: inherit;
  font-family: inherit;
}

button.bf-banner:hover {
  background-color: var(--bfc-theme-2);
}

button.bf-banner[data-focus-visible-added] {
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 1px;
}

button.bf-banner:active {
  transform: translateY(2px) translateX(-1px);
}

button.bf-banner:active::before {
  left: -4px;
  border-bottom-width: 6px;
  border-left-width: 4px;
}

button.bf-banner:active::after {
  content: '';
  position: absolute;
  top: -2px;
  right: -1px;
  display: block;
  width: 100%;
  height: 100%;
  background-color: transparent;
}
