html {
  box-sizing: border-box;
}
*,
*:after,
*:before {
  box-sizing: inherit;
}
:root {
  /* spacing */
  --bfs0: 0rem;
  --bfs2: 0.125rem;
  --bfs4: 0.25rem;
  --bfs6: 0.375rem;
  --bfs8: 0.5rem;
  --bfs12: 0.75rem;
  --bfs16: 1rem;
  --bfs24: 1.5rem;
  --bfs28: 1.75rem;
  --bfs32: 2rem;
  --bfs40: 2.5rem;
  --bfs48: 3rem;
  --bfs52: 3.25rem;
  --bfs56: 3.5rem;
  --bfs80: 5rem;

  --bf-font-size-s: 12px; /* small */
  --bf-font-size-m: 14px; /* p, body, label */
  --bf-font-size-l: 16px; /* input, h5, .bf-large */
  /* todo: next major release, remove xl-xxxxl vars in favor of h1-h5 */
  --bf-font-size-xl: 19px; /* h4 */
  --bf-font-size-xxl: 22px; /* h3 */
  --bf-font-size-xxxl: 26px; /* h2 */
  --bf-font-size-xxxxl: 31px; /* h1 */
  --bf-font-size-h1: var(--bf-font-size-xxxxl);
  --bf-font-size-h2: var(--bf-font-size-xxxl);
  --bf-font-size-h3: var(--bf-font-size-xxl);
  --bf-font-size-h4: var(--bf-font-size-xl);
  --bf-font-size-h5: var(--bf-font-size-l);

  --bfl-border-radius: 0.1875rem; /* 3px */

  --bf-animation-duration: 0.2s;
  --bf-timing-function: ease;

  /* todo: remove with sidebar and topbar */
  --bf-topbar-height: var(--bfs56);
  --bf-sidebar-width: 14.375rem;
  --bf-sidebar-width-collapsed: var(--bfs56);
  --bf-sidebar-transition: var(--bf-animation-duration)
    var(--bf-timing-function);
}

/* todo: test if this works as intended. does not affect js animations */
@media (prefers-reduced-motion) {
  :root {
    --bf-animation-duration: 0s;
  }
}

:root,
.bf-darkmode {
  --bfl-border: 1px solid var(--bfc-base-dimmed);
  --bf-form-focus-outline: 0 0 0 4px var(--bfc-base-dimmed);
}
.bf-lightmode {
  --bfl-border: 1px solid var(--bfc-base-dimmed);
  --bf-form-focus-outline: 0 0 0 4px var(--bfc-base-dimmed);
}

/* basic non-responsive bifrost layout */
.bfl-inline,
.bfl,
.bfl-2col,
.bfl-3col {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: -1rem;
}
.bfl-inline > * {
  margin: 0 1rem 1rem 0;
}
.bfl > *,
.bfl-2col > *,
.bfl-3col > * {
  padding-bottom: 1rem;
}
.bfl > * {
  width: 100%;
}
.bfl-2col > * {
  width: 50%;
}
.bfl-2col > :nth-child(2n + 1) {
  padding-right: 0.5rem;
}
.bfl-2col > :nth-child(2n) {
  padding-left: 0.5rem;
}
.bfl-3col > * {
  width: 33.333%;
}
.bfl-3col > :nth-child(3n + 1) {
  padding-right: 0.667rem;
}
.bfl-3col > :nth-child(3n + 2) {
  padding-right: 0.333rem;
  padding-left: 0.333rem;
}
.bfl-3col > :nth-child(3n) {
  padding-left: 0.667rem;
}

/* responsive modal padding */
:root {
  --bfl-modal-padding: var(--bfs40);
}

@media (max-width: 599.9px), (max-height: 599.9px) {
  :root {
    --bfl-modal-padding: var(--bfs24);
  }
}

/* responsive page padding */
:root {
  /* page padding changes at 600px and 1920px screen width. 12 - 24 - 40 */
  --bfl-page-padding: var(--bfs12);
}
.bfl-page-padding {
  padding: var(--bfl-page-padding, 12px);
}
.bfl-page-padding-x {
  padding-left: var(--bfl-page-padding);
  padding-right: var(--bfl-page-padding);
}
.bfl-page-padding-y {
  padding-top: var(--bfl-page-padding);
  padding-bottom: var(--bfl-page-padding);
}
.bfl-page-padding.bf-notch,
.bfl-page-padding-x.bf-notch {
  padding-left: calc(var(--bfl-page-padding) + env(safe-area-inset-left));
  padding-right: calc(var(--bfl-page-padding) + env(safe-area-inset-right));
}
@media (min-width: 600px) {
  :root {
    --bfl-page-padding: var(--bfs24);
  }
}
@media (min-width: 1920px) {
  :root {
    --bfl-page-padding: var(--bfs40);
  }
}

/* responsive box padding */
:root {
  /* box padding changes at 1280px screen width. 16 - 24 */
  --bfl-box-padding: var(--bfs16);
}
.bfl-padding {
  padding: var(--bfl-box-padding);
}
.bfl-padding-x {
  padding-left: var(--bfl-box-padding);
  padding-right: var(--bfl-box-padding);
}
.bfl-padding-y {
  padding-top: var(--bfl-box-padding);
  padding-bottom: var(--bfl-box-padding);
}
.bfl-padding.bf-notch,
.bfl-padding-x.bf-notch {
  padding-left: calc(var(--bfl-box-padding) + env(safe-area-inset-left));
  padding-right: calc(var(--bfl-box-padding) + env(safe-area-inset-right));
}
@media (min-width: 1280px) {
  :root {
    --bfl-box-padding: var(--bfs24);
  }
}

/* responsive automatic columns
number of columns will be available width divided by autocol-width
each column will stretch to take up available space
if available width is less than autocol-width, minimum column width will not exceed 100%
prefer css grids because `gap` is well supported (compiles to include `grid-gap`), but not for flexbox or css columns
read more: https://css-tricks.com/snippets/css/complete-guide-grid/ */
:root {
  --bfl-autocol-width: 300px;
  --bfl-autocol-gap: var(--bfs12, 12px);
}
.bfl-autocol {
  display: grid;
  gap: var(--bfl-autocol-gap);
  grid-template-columns: repeat(
    auto-fill,
    minmax(min(var(--bfl-autocol-width), 100%), 1fr)
  );
}

/* column grid */
:root {
  /* used to configure column count for .bfl-grid,
     and calculate --bf-card-image-height based on a ratio */
  --bfl-columns: 1;
}
.bfl-grid {
  --bfl-gap: var(--bfs12, 12px);
  display: grid;
  gap: var(--bfl-gap);
  grid-template-columns: repeat(var(--bfl-columns, 1), minmax(0, 1fr));
}
