.bf-floatingmessage-stack {
  position: fixed;
  bottom: var(--bfl-page-padding, 1rem);
  right: var(--bfl-page-padding, 1rem);
  z-index: 2;
  transition: left 300ms;
  text-align: right;
  pointer-events: none;
}

.bf-floatingmessage-stack-left {
  right: auto;
  left: calc(var(--bfl-page-padding, 1rem) + var(--bf-nav-side-width, 0px));
  text-align: left;
}
.bf-floatingmessage-stack-top {
  bottom: auto;
  top: calc(var(--bfl-page-padding, 1rem) + var(--bf-nav-top-height, 0px));
}

.bf-floatingmessage-stack .bf-message {
  display: inline-block;
  position: relative;
  padding-right: 42px;
  box-shadow: var(--bf-shadow);
  pointer-events: auto;
  text-align: left;
}
.bf-floatingmessage-stack .bf-message-close {
  position: absolute;
  right: 10px;
}
.bf-floatingmessage-stack .bf-message-header {
  margin-bottom: 0;
}
.bf-floatingmessage-stack .bf-message-content:not(:empty) {
  margin-top: var(--bfs4);
}

.bf-floatingmessage-enter {
  opacity: 0;
  margin-top: 0;
  height: 0;
  transform: scale(0.9);
}
.bf-floatingmessage-enter-active,
.bf-floatingmessage-enter-done {
  opacity: 1;
  margin-top: var(--bfs8);
  /* approximate height, causes a flicker for messages with 2+ lines */
  height: 45px;
  transform: translateX(0);
  transition-property: opacity, transform, margin-top, height;
  transition-duration: 300ms;
}
.bf-floatingmessage-enter-done {
  height: auto;
}

.bf-floatingmessage-exit {
  opacity: 1;
  margin-top: var(--bfs8);
  height: 45px;
}
.bf-floatingmessage-exit-active {
  opacity: 0;
  margin-top: 0;
  height: 0;
  transform: scale(0.9);
  transition-property: opacity, transform, margin-top, height;
  transition-duration: 300ms;
}
