.bf-breadcrumbs {
  margin: 0;
  padding: 0;
  font-size: var(--bf-font-size-m);
}
.bf-breadcrumbs > * {
  color: var(--bft-base-2);
  list-style: none;
  display: inline-block;
}
.bf-breadcrumbs > li + li:before {
  content: '\27E9'; /* right angle */
  margin-right: var(--bfs8);
}

.bf-breadcrumbs a,
.bf-breadcrumbs a:link,
.bf-breadcrumbs a:visited,
.bf-breadcrumbs .bf-link,
.bf-breadcrumbs .bf-link:link,
.bf-breadcrumbs .bf-link:visited {
  color: var(--bft-base);
  text-decoration: none;
}
.bf-breadcrumb-angle {
  color: var(--bft-base);
  font-size: var(--bf-font-size-l);
  margin: 0 var(--bfs12);
}
.bf-breadcrumbs a:hover,
.bf-breadcrumbs .bf-link:hover {
  color: var(--bft-theme);
  text-decoration: underline;
  background-color: transparent;
  box-shadow: none;
}
.bf-breadcrumbs a[data-focus-visible-added],
.bf-breadcrumbs .bf-link[data-focus-visible-added] {
  border-radius: 1px;
  outline: 1px dashed var(--bfc-base-c-theme);
  outline-offset: 3px;
}
.bf-breadcrumb-angle:first-child {
  display: none;
}

/* BackButton */
.bf-breadcrumbs button.bf-link {
  background: transparent;
  border: none;
  font: inherit;
  display: inline;
}
