.bf-close-button {
  position: absolute;
  right: var(--bfl-modal-padding);
  top: var(--bfl-modal-padding);
  font-size: var(--bf-font-size-xxxl);
  color: var(--bfc-base-c-theme);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  text-overflow: unset;
  white-space: unset;
  background: inherit;
}
.bf-close-button:hover,
.bf-close-button[data-focus-visible-added]:hover {
  background: var(--bfc-base-dimmed);
}
.bf-close-button[data-focus-visible-added] {
  outline: none;
  box-shadow: none;
  border: 1px dashed var(--bff-theme);
  background-color: transparent;
}
