.bf-textarea-container,
.bf-input-container,
.bf-select-container,
.bf-datepicker-container {
  font-size: var(--bf-font-size-m);
}

/* REQUIRED TEXT */
.bf-label-required {
  color: var(--bfc-base-c-2);
  font-weight: normal;
  font-style: italic;
  margin-left: var(--bfs4);
}

/* DESCRIPTION */
.bf-label-description {
  color: var(--bfc-base-c-2);
  font-weight: normal;
  font-size: var(--bf-font-size-m);
}

/* PLACEHOLDER TEXT */ 
.bf-input::placeholder,
.bf-textarea::placeholder,
.bf-datepicker::placeholder {
  color: var(--bfc-base-c-2);
}

/* DISABLED */
.bf-input-disabled.bf-input-container .bf-input,
.bf-input-disabled.bf-input-container .bf-input:hover,
.bf-datepicker-disabled.bf-datepicker-container .bf-datepicker,
.bf-datepicker-disabled.bf-datepicker-container .bf-datepicker:hover,
.bf-textarea:disabled,
.bf-textarea-disabled.bf-textarea-container .bf-textarea:hover {
  cursor: default;
  color: var(--bfc-base-c);
  border-bottom-color: transparent;
  background: var(--bfc-base-dimmed);
}
.bf-input-disabled.bf-input-container .bf-input::placeholder,
.bf-datepicker-disabled.bf-datepicker-container .bf-datepicker::placeholder,
.bf-textarea:disabled::placeholder {
  color: var(--bfc-base-c-2);
}

/* DISABLED LABEL ICON */
.bf-label > .bf-input-disabled-icon {
  color: var(--bfc-base-c-2);
  margin: 0px var(--bfs4);
}

/* INPUT FIELD */
.bf-textarea,
.bf-input,
.bf-datepicker {
  background: var(--bfc-base-3);
  color: var(--bfc-base-c);
  border: 0;
  border-bottom: 2px solid var(--bfc-base-c-wcag);
  border-radius: var(--bfl-border-radius);
  font-family: inherit;
  font-size: var(--bf-font-size-l);
  padding: var(--bfs8) var(--bfs12);
  width: 100%;
}

/* INPUT FIELD HOVER */
.bf-textarea:hover,
.bf-input:hover,
.bf-datepicker:hover {
  border-bottom-color: var(--bfc-base-c-inverted-2);
}

/* INPUT FIELD FOCUS */
.bf-textarea:focus,
.bf-input:focus,
.bf-input-focus,
.bf-datepicker:focus {
  border-bottom-color: var(--bfc-base-c-theme);
  outline: none;
  box-shadow: 0 0 0 1px var(--bfc-theme-3);
}

/* FEEDBACK TEXT */
.bf-label-feedback {
  color: var(--bfc-base-c);
  text-align: right;
  font-size: var(--bf-font-size-s);
  margin-top: 4px;
}
.bf-label-feedback > :first-child {
  margin: 0;
}

/* WARNING SVG COLOR */
.bf-textarea-warning svg,
.bf-datepicker-warning svg {
  color: var(--bfc-alert-c-2);
}

/* ALERT BORDER BOTTOM COLOR */
.bf-input-alert .bf-input,
.bf-textarea-alert .bf-textarea,
.bf-select-alert .bf-select__control,
.bf-datepicker-alert .bf-datepicker {
  border-bottom-color: var(--bfc-base-c-alert);
}
.bf-input-alert .bf-input:hover,
.bf-textarea-alert .bf-textarea:hover,
.bf-select-alert .bf-select__control:hover,
.bf-datepicker-alert .bf-datepicker:hover {
  border-bottom-color: var(--bfc-alert-fade-c);
}
.bf-input-alert .bf-input:focus,
.bf-textarea-alert .bf-textarea:focus,
.bf-select-alert .bf-select__control.bf-select__control--is-focused,
.bf-datepicker-alert .bf-datepicker:focus {
  border-bottom-color: var(--bfc-base-c-theme);
}

/* ALERT SVG COLOR */
.bf-textarea-alert svg,
.bf-datepicker-alert svg {
  color: var(--bfc-base-c-alert);
}

/* label, fieldset and legend */
.bf-fieldset,
.bf-content fieldset {
  padding: 0;
  margin: 0;
  border: none;
}
.bf-legend,
.bf-content legend {
  font-size: var(--bf-font-size-h3);
  color: var(--bfc-base-c-2);
  padding: 0;
  margin: 0 0 var(--bfs16) 0;
}
.bf-label,
.bf-content label,
.bf-content .bf-label {
  font-size: var(--bf-font-size-m);
  font-weight: 600;
  color: var(--bfc-base-c);
  display: block;
}

/* space between label and input */
.bf-legend.bf-label,
.bf-content legend.bf-label {
  margin: 0 0 var(--bfs4) 0;
}
.bf-label ~ .bf-input,
.bf-label ~ .bf-input-icon-container,
.bf-label ~ .bf-textarea,
.bf-label ~ .bf-textarea-icon-container,
.bf-label ~ .bf-datepicker-icon-container {
  margin-top: var(--bfs4);   /* Select gets this space from selectStyles object. See Select.tsx */ 
}

/* contrasting background color for inputs inside '.bfc-base-3-bg'  */
.bfc-base-3-bg .bf-textarea,
.bfc-base-3-bg .bf-input,
.bfc-base-3-bg .bf-datepicker,
.bfc-base-3-bg .bf-select__control {
  background-color: var(--bfc-base-2);
}

/* Make sure Inputs inside nested base-3-bg has the correct contrast */
.bfc-base-2-bg .bf-textarea,
.bfc-base-2-bg .bf-input,
.bfc-base-2-bg .bf-datepicker,
.bfc-base-2-bg .bf-select__control {
  background-color: var(--bfc-base-3);
}
