.bf-accordion-container {
  border: 1px solid var(--bff-dimmed);
  border-radius: var(--bfl-border-radius);
}
.bf-accordion-noborder {
  border: none;
  border-radius: 0;
}

.bf-accordion-item {
  background: var(--bfc-base-3);
  color: var(--bfc-base-c);
}
.bf-accordion-item + .bf-accordion-item {
  border-top: 1px solid var(--bff-dimmed);
}

.bf-accordion-item-title {
  font-size: var(--bf-font-size-m);
  padding: 15px var(--bfs12);
  white-space: nowrap;
  cursor: pointer;
  background: transparent;
  border: 0;
  width: 100%;
  text-align: left;
  color: inherit;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: center;
}

.bf-accordion-styled .bf-accordion-item-no-icon .bf-accordion-item-title {
  grid-template-columns: 1fr auto;
}
.bf-accordion-container:not(.bf-accordion-styled) .bf-accordion-item-title {
  grid-template-columns: auto auto 1fr;
}

.bf-accordion-item-title:focus {
  outline: none;
}

.bf-accordion-item:last-child .bf-accordion-item-title,
.bf-accordion-item:last-child .bf-accordion-item-content {
  border-bottom: 0;
}

.bf-accordion-item-title > .bf-neutral-link-text {
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: var(--bfs8);
}

.bf-accordion-item-title .bf-accordion-item-angle {
  font-size: var(--bf-font-size-m);
}

/* Font awesome icon animation */
.bf-accordion-item-title .bf-accordion-item-angle {
  transition: all var(--bf-animation-duration);
}

.bf-accordion-item-active .bf-accordion-item-angle {
  transform: rotate(90deg);
}

.bf-accordion-item-content {
  padding: var(--bfs16);
  background-color: var(--bfc-base-2);
  word-wrap: break-word;
  font-size: var(--bf-font-size-m);
  position: relative;
}
.bf-accordion-item-content.bfl-nopadding {
  padding: 0;
}

.bf-accordion-item-title:hover {
  color: var(--bfc-theme-1);
}
.bf-accordion-item-title[data-focus-visible-added] {
  outline: 1px dashed var(--bff-theme);
  -moz-outline-radius: var(--bfl-border-radius);
  /* make sure the outline is visible on top of expanded accordion item content */
  position: relative;
  z-index: 1;
}

/* Disabled */
.bf-accordion-item-disabled,
.bf-accordion-item-disabled .bf-accordion-item-title svg,
.bf-accordion-item-disabled .bf-accordion-item-angle-wrapper svg {
  pointer-events: none;
  color: var(--bfc-base-c-disabled);
}

.bf-accordion-item-icon,
.bf-accordion-item-angle {
  margin-right: var(--bfs8);
}

/* #region styled variant */
.bf-accordion-styled {
  border: none;
}
.bf-accordion-styled .bf-accordion-item {
  border: 1px solid var(--bff-dimmed);
  border-radius: var(--bfl-border-radius);
}
.bf-accordion-styled.bf-accordion-noborder .bf-accordion-item {
  border: none;
}
.bf-accordion-styled .bf-accordion-item + .bf-accordion-item {
  margin-top: var(--bfs8);
}

.bf-accordion-styled .bf-accordion-item-title {
  padding: 18px var(--bfs16);
}
.bf-accordion-styled .bf-accordion-item-title:hover {
  color: inherit;
}
.bf-accordion-styled .bf-accordion-item-title:hover .bf-neutral-link-text {
  text-decoration: underline;
}
.bf-accordion-styled .bf-accordion-item-icon {
  color: var(--bfc-base-c-2);
}

.bf-accordion-item-angle-wrapper {
  justify-self: end;
}
.bf-accordion-item-angle-wrapper .bf-accordion-item-angle {
  color: var(--bff-theme);
  transform: rotate(90deg);
  margin-left: var(--bfs8);
  margin-right: 0;
}
.bf-accordion-item-active
  .bf-accordion-item-angle-wrapper
  .bf-accordion-item-angle {
  transform: rotate(-90deg);
}
/* #endregion */

/* #region responsive / compact */
.bf-accordion-compact {
  border-left: none;
  border-right: none;
  border-radius: 0;
}
.bf-accordion-compact.bf-accordion-styled .bf-accordion-item {
  border: none;
  border-radius: 0;
}
@media (max-width: 599.9px) {
  .bf-accordion-responsive {
    border-left: none;
    border-right: none;
    border-radius: 0;
  }
  .bf-accordion-responsive.bf-accordion-styled .bf-accordion-item {
    border: none;
    border-radius: 0;
  }
}
/* #endregion */
